import * as rpc from './rpc';

let timmer;

// export const setIO = (url) =>
// {
//     Io = io(url);
// };

const refreshInterval = (actions) =>
{
    timmer = setInterval(() =>
    {
        actions.userActions.accountGet$();
    }, 15000);
};

export const loginIO = (wallet, password, key, tokenList, actions, testWallet, testKey) =>
{
    return new Promise((resolve) =>
    {
        const { userActions } = actions;
        if (key)
        {
            userActions.userGet$({ wallet, password, key });
        }
        else
        {
            userActions.userGet$({ wallet, observer: true });
        }

        window.dbObj.ref('operatorBooks').orderByChild('user').equalTo(`0x${wallet.address}`).on('value', (snapshot) =>
        {
            userActions.historyGet$(Object.values(snapshot.val() || []));
        });

        // actions.userActions.accountGet$();

        // show money for test
        if (testWallet)
        {
            rpc.showMeMoney(testWallet.address, wallet.address, testKey).then(() =>
            {
                // this.refreshInterval();
            });
        }
        refreshInterval(actions);
        resolve();
    });
};

export const clearTimeInterval = () =>
{
    clearInterval(timmer);
};

