import React from 'react';
import PropTypes from 'prop-types';
import update from 'immutability-helper';
import { translate } from 'react-i18next';

import { loginIO } from '../../utils/login';

class ImportWallet extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            downloadHref: '',
            password: '',
            wallet: '',
            message: '',
            passwordType: true // if true, type === 'password'
        };

        this.readFile = this.readFile.bind(this);
        this.submitImport = this.submitImport.bind(this);
        this.handleChange = this.handleChange.bind(this);

        this.wallet = null;
    }

    handleChange(e)
    {
        this.setState(update(this.state, {
            password: { $set: e.target.value },
            message: { $set: '' }
        }));
    }

    readFile()
    {
        const file = this.import.files[0];
        const reader = new FileReader();

        reader.onload = (() =>
        {
            return (e) =>
            {
                this.wallet = JSON.parse(e.target.result);
                this.setState(update(this.state, {
                    wallet: { $set: this.wallet },
                    message: { $set: '' }
                }));
                let keys = Object.keys(this.wallet).map(v => v.toLowerCase());
                if (keys.indexOf('address') === -1 || keys.indexOf('id') === -1 || keys.indexOf('crypto') === -1)
                {
                    const { t } = this.props;
                    this.setState(update(this.state, {
                        message: { $set: t('walletError') }
                    }));
                }
                this.import.value = '';
            };
        })(file);
        reader.readAsText(file);
    }

    submitImport()
    {
        const { actions, t, tokenList } = this.props;
        const { wallet, password } = this.state;

        if (!this.wallet) return this.setState(update(this.state, { message: { $set: t('importAlert') } }));
        const recoverKey = () =>
        {
            return new Promise((resolve, reject) =>
            {
                const worker = new Worker('/js/recoverKeyWorker.js');
                worker.onmessage = (e) =>
                {
                    setTimeout(() =>
                    {
                        if (e.data === 'error') reject();
                        this.key = e.data;
                        resolve(this.key);
                    }, 300);
                };
                worker.postMessage({ wallet, password });
            });
        };

        actions.modalActions.updateLoading(true);
        actions.modalActions.updateMessage(t('importing'));

        return recoverKey().then(() =>
        {
            this.setState(update(this.state, {
                downloadHref: { $set: '' }
            }));
            loginIO(wallet, password, this.key, tokenList, actions, '', '').then(() =>
            {
                actions.modalActions.updateLoading(false);
                this.props.modalControl.close();
            });
        }).catch((err) =>
        {
            console.warn(err);
            this.setState(update(this.state, {
                message: { $set: t('importError') }
            }));
            actions.modalActions.updateLoading(false);
        });
    }

    render()
    {
        const { passwordType, wallet, message } = this.state;
        const { t } = this.props;

        return (
            <div className="c_importWallet">
                <p className="modal-title">
                    {t('importWallet')}
                </p>
                <a className="import">
                    <input className="import-input" ref={(input) => { this.import = input; }} accept=".json" type="file" onChange={this.readFile} disabled={this.props.loading} />
                    {t('importWallet')}
                </a>
                <div className="modal-content">
                    <div className="address">
                        <div>{t('address')}</div>
                        <div>{wallet.address ? wallet.address : ''}</div>
                    </div>
                    <div className="modal-input">
                        <label htmlFor="password">
                            {t('yourPassword')}
                        </label>
                        <div className={message === '' ? '' : 'error'} data-message={message}>
                            <input type={passwordType ? 'password' : 'text'} onChange={this.handleChange} disabled={this.props.loading} required />
                        </div>
                    </div>
                    <div>
                        <label htmlFor="show-password">
                            <input type="checkbox" onChange={() => { this.setState(update(this.state, { passwordType: { $set: !this.state.passwordType } })); }} />
                            &nbsp;&nbsp;{t('showPassword')}
                        </label>
                    </div>
                </div>
                <div className="modal-btn">
                    <div className="button" onClick={this.props.modalControl.close}>
                        <span>{t('cancel')}</span>
                    </div>
                    <div className="button" onClick={this.submitImport}>
                        <span>{t('import')}</span>
                    </div>
                </div>
            </div>
        );
    }
}

ImportWallet.defaultProp = {
    t: () => {}
};

ImportWallet.propTypes = {
    t: PropTypes.func.isRequired,
    actions: PropTypes.object.isRequired,
    loading: PropTypes.bool.isRequired,
    modalControl: PropTypes.object.isRequired,
    tokenList: PropTypes.array.isRequired
};

export default translate('account', { wait: true })(ImportWallet);
