import React from 'react';
import PropTypes from 'prop-types';
import { ChartCanvas, Chart } from 'react-stockcharts';
import { BarSeries, CandlestickSeries, LineSeries } from 'react-stockcharts/lib/series';
import { XAxis, YAxis } from 'react-stockcharts/lib/axes';
import { discontinuousTimeScaleProvider } from 'react-stockcharts/lib/scale';
import { fitWidth } from 'react-stockcharts/lib/helper';
import { last } from 'react-stockcharts/lib/utils';
import { HoverTooltip } from 'react-stockcharts/lib/tooltip';
import { timeFormat } from 'd3-time-format';
import { translate } from 'react-i18next';
import {
    CurrentCoordinate,
} from 'react-stockcharts/lib/coordinates';
import { formatNumber } from '../../utils/format';
// import { ema } from "react-stockcharts/lib/indicator";

const dateFormat = timeFormat('%Y-%m-%d-%H:%M:%S');
class KChart extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {};
        this.range = ['10minute', '1hr', '1day', '1month'];
        this.unit = 0;

        this.tooltipContent = this.tooltipContent.bind(this);
    }

    tooltipContent()
    {
        const { t } = this.props;
        return ({ currentItem, xAccessor }) =>
        {
            return {
                x: dateFormat(xAccessor(currentItem)),
                y: [
                    {
                        label: `${t('chart.opPrice')} `,
                        value: formatNumber(9, currentItem.open)
                    },
                    {
                        label: `${t('chart.htPrice')} `,
                        value: formatNumber(9, currentItem.high)
                    },
                    {
                        label: `${t('chart.ltPrice')} `,
                        value: formatNumber(9, currentItem.low)
                    },
                    {
                        label: `${t('chart.csPrice')} `,
                        value: formatNumber(9, currentItem.close)
                    },
                    {
                        label: `${t('chart.amount')} `,
                        value: formatNumber(9, currentItem.amount)
                    },
                    {
                        label: '10ma ',
                        value: formatNumber(9, currentItem['10ma']),
                        stroke: '#f8e71c'
                    },
                    {
                        label: '5ma ',
                        value: formatNumber(9, currentItem['5ma']),
                        stroke: '#77479e'
                    }
                ]
            };
        };
    }


    render()
    {
        const { data: initialData, width, height, ratio, margin } = this.props;
        const xScaleProvider = discontinuousTimeScaleProvider.inputDateAccessor(d => d.timestamp);
        const { data, xScale, xAccessor, displayXAccessor } = xScaleProvider(initialData);

        if (width < 101 || initialData.length < 1)
        {
            return <div />;
        }

        // const ema50 = ema()
        // .id(0)
        // .options({ windowSize: 50 })
        // .merge((d, c) => { d.ema50 = c; })
        // .accessor(d => d.ema50);

        const xExtents = [
            xAccessor(last(data)),
            xAccessor(data[0])
        ];
        const candlesAppearance = {
            wickStroke: function stroke(d)
            {
                return d.close < d.open ? '#f44173' : '#27ca7b';
            },
            fill: function fill(d)
            {
                return d.close < d.open ? '#f44173' : '#27ca7b';
            },
            stroke: function stroke(d)
            {
                return d.close < d.open ? '#f44173' : '#27ca7b';
            },
            candleStrokeWidth: 1,
            widthRatio: 0.8,
            opacity: 1,
        };

        // const gridHeight = height - margin.top - margin.bottom;
        const gridWidth = width - margin.left - margin.right;
        const yGrid = {
            innerTickSize: -1 * gridWidth,
            tickStrokeDasharray: 'Dot'
        };

        const grid = {
            innerTickSize: -1 * gridWidth,
            tickStrokeOpacity: 0
        };

        // const xGrid = { innerTickSize: -1 * gridHeight };
        return (
            <ChartCanvas
                ref={(node) => { this.node = node; }}
                height={height}
                ratio={ratio}
                width={width}
                margin={margin}
                seriesName="MSFT"
                data={data}
                xScale={xScale}
                xAccessor={xAccessor}
                displayXAccessor={displayXAccessor}
                xExtents={xExtents}
                clamp={true}
                pointsPerPxThreshold={1}
                // zoomEvent={false}
            >
                <Chart id={1} yExtents={d => [d.high * 1.05, d.low * 1.3]} height={height - 140}>
                    <XAxis axisAt="bottom" orient="bottom" ticks={6} stroke="rgba(255, 255, 255, .5)" tickStroke="rgba(255, 255, 255, .5)" showTicks={false} />
                    <YAxis axisAt="right" orient="right" ticks={5} stroke="#8a8a8a" tickStroke="#8a8a8a" {...yGrid} />
                    <LineSeries yAccessor={d => d['5ma']} stroke="#77479e" />
                    <LineSeries yAccessor={d => d['10ma']} stroke="#f8e71c" />
                    <CurrentCoordinate yAccessor={d => d['5ma']} fill="#77479e" />
                    <CurrentCoordinate yAccessor={d => d['10ma']} fill="#f8e71c" />
                    <CandlestickSeries {...candlesAppearance} />
                    <HoverTooltip
                        tooltipContent={this.tooltipContent()}
                        fill="#000"
                        bgFill="#D4E2FD"
                        bgOpacity={0.5}
                        stroke="#000"
                        fontFill="#ddd"
                        opacity={0.6}
                        fontSize={14}
                    />
                </Chart>
                <Chart id={2} origin={(w, h) => [0, h - 90]} height={90} yExtents={d => d.amount * 1.2}>
                    <XAxis axisAt="bottom" orient="bottom" stroke="#8a8a8a" tickStroke="#8a8a8a" {...grid} />
                    <YAxis axisAt="right" orient="right" ticks={3} stroke="#8a8a8a" tickStroke="#8a8a8a" {...grid} />
                    <BarSeries yAccessor={d => d.amount} />
                </Chart>
            </ChartCanvas>
        );
    }
}

KChart.defaultProps = {
    t: () => {}
};

KChart.propTypes = {
    data: PropTypes.array.isRequired,
    width: PropTypes.number.isRequired,
    height: PropTypes.number.isRequired,
    ratio: PropTypes.number.isRequired,
    margin: PropTypes.object.isRequired,
    t: PropTypes.func
};

export default translate()(fitWidth(KChart));
