import React from 'react';
import PropTypes from 'prop-types';
import update from 'immutability-helper';
import equal from 'deep-equal';
import { translate } from 'react-i18next';
import { formatNumber, formatTime } from '../../utils/format';
import './recentTransaction.scss';

class RecentTransaction extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            toggle: true
        };
    }

    shouldComponentUpdate(nextProps, nextState)
    {
        if (equal(nextProps, this.props) && equal(nextState, this.state))
        {
            return false;
        }
        else
        {
            return true;
        }
    }

    renderTable()
    {
        const { transaction, t, from, to } = this.props;
        const content = transaction.map((data, index) =>
        {
            // const priceClass = data.isRising ? 'up' : 'down';
            return (
                <div key={data.index || index} className="info">
                    <div className={data.type === 'buy' ? 'green' : 'red'}>
                        {data.type === 'buy' ? t('table.buy') : t('table.sell')}
                    </div>
                    <div className="amount">{formatNumber(8, data.displayPrice)}</div>
                    <div>{formatNumber(7, data.amount)}</div>
                    <div className="time">{formatTime(data.timestamp, 'time')}</div>
                </div>
            );
        });
        return (
            <div>
                <div className="title">
                    {t('table.recentTranssaction')}
                </div>
                <div className={this.state.toggle ? 'table' : 'table close'}>
                    <div className="t-header">
                        <span />
                        <span>{t('table.price')}({to.token})</span>
                        <span>{t('table.volume')}({from.token})</span>
                        <span>{t('table.time')}</span>
                    </div>
                    <div className="t-body">
                        {content}
                    </div>
                </div>
            </div>
        );
    }

    render()
    {
        return (
            <div className="c_recentTransaction">
                {this.renderTable()}
            </div>
        );
    }
}

RecentTransaction.defaultProps = {
    t: () => {}
};

RecentTransaction.propTypes = {
    transaction: PropTypes.array.isRequired,
    t: PropTypes.func,
    from: PropTypes.object.isRequired,
    to: PropTypes.object.isRequired
};

export default translate()(RecentTransaction);
