import React from 'react';
import PropTypes from 'prop-types';
import { translate } from 'react-i18next';
import { scnToStr } from '../../utils/format';

class Mortgage extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {};
    }

    render()
    {
        const { value, t } = this.props;
        const method = () =>
        {
            switch (value.type)
            {
                case 'mortgage':
                    return t('mortgage.Mortgage');
                case 'withdraw':
                    return t('mortgage.Withdraw');
                case 'repayment':
                    return t('mortgage.Repayment');
                default:
                    return '';
            }
        };
        return (
            <div>
                <div className="modal-title">
                    {method()}{value.type === 'mortgage' ? value.fromToken.token : value.toToken.token}
                </div>
                <div className="modal-content">
                    <div className="detail">
                        <div>
                            {/* <div>{t('actionMessage')}{value.type === 'buy' ? `${t('buy')}${value.toToken.token}` : `${t('sell')}${value.fromToken.token}`}</div> */}
                            <div>{value.type === 'mortgage' ? `${value.fromToken.amount} ${value.fromToken.token}` : `${value.toToken.amount} ${value.toToken.token}`}</div>
                            <div>{t('gas')}  {scnToStr(value.gas)} ETH</div>
                            <div>{t('fee')}  {value.fee ? `${value.fee} ${value.toToken.token}` : 0}</div>
                        </div>
                        <img src="/img/walletalert.png" alt="wallet" />
                    </div>
                </div>
                <div className="modal-btn">
                    <div className="button" onClick={() => { value.exec(); }}>
                        <span>{method()}{value.type === 'mortgage' ? value.fromToken.token : value.toToken.token}</span>
                    </div>
                </div>
            </div>
        );
    }
}

Mortgage.defaultProps = {
    t: () => {}
};

Mortgage.propTypes = {
    value: PropTypes.object.isRequired,
    t: PropTypes.func
};

export default translate('account', { wait: true })(Mortgage);
