import React from 'react';
import { translate } from 'react-i18next';
import PropTypes from 'prop-types';
import ImportWallet from './importWallet';
import CreateWallet from './createWallet';
import ExportWallet from './exportWallet';
import Observer from './observer';
import ObserverQr from './observerQr';
import Transaction from './transaction';
import CancelOrder from './cancelOrder';
import CheckPassword from './checkPassword';
import Receipt from './receipt';
import Mortgage from './mortgage';
import Offset from './offset';
import './modal.scss';

@translate(['common'], { wait: true })
class Modal extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            fade: ''
        };
    }

    componentDidMount()
    {
        setTimeout(() =>
        {
            this.setState({
                fade: 'fadeIn'
            });
        }, 1);
    }

    renderView()
    {
        const { actions, loading, tokenList, modalControl, value, user, toastOpen } = this.props;
        
        if (this.state.fade)
        {
            let view = null;
            switch (this.props.view)
            {
                case 'import':
                    return (
                        <ImportWallet
                            actions={actions}
                            loading={loading}
                            tokenList={tokenList}
                            modalControl={modalControl}
                        />
                    );
                case 'create':
                    return (
                        <CreateWallet
                            actions={actions}
                            loading={loading}
                            tokenList={tokenList}
                            modalControl={modalControl}
                            toastOpen={toastOpen}
                        />
                    );
                case 'observer':
                    return (
                        <Observer
                            actions={actions}
                            loading={loading}
                            modalControl={modalControl}
                            tokenList={tokenList}
                        />
                    );
                case 'observerQr':
                    return (
                        <ObserverQr
                            value={value}
                            modalControl={modalControl}
                            user={user}
                            actions={actions}
                        />
                    );
                case 'transaction':
                    return (
                        <Transaction
                            actions={actions}
                            value={value}
                        />
                    );
                case 'cancelOrder':
                    return (
                        <CancelOrder
                            value={value}
                        />
                    );
                case 'checkPassword':
                    return (
                        <CheckPassword
                            modalControl={modalControl}
                            value={value}
                        />
                    );

                case 'exportWallet':
                    return (
                        <ExportWallet
                            modalControl={modalControl}
                            value={value}
                            user={user}
                            actions={actions}
                        />
                    );
                case 'mortgage':
                    return (
                        <Mortgage
                            modalControl={modalControl}
                            value={value}
                            user={user}
                            actions={actions}
                        />
                    );
                case 'offset':
                    return (
                        <Offset
                            modalControl={modalControl}
                            value={value}
                            user={user}
                            actions={actions}
                        />
                    );
                case 'receipt':
                    return (
                        <Receipt
                            modalControl={modalControl}
                            value={value}
                        />
                    );
                default:
            }

            return view;
        }
        else
        {
            return null;
        }
    }

    render()
    {
        return (
            <div className={`c_modal ${this.state.fade}`}>

                {/* content */}
                <div className="content">

                    {/* head */}
                    <div className="head">
                        <div
                            onClick={() =>
                            {
                                if (this.props.keep) return this.props.toastOpen(this.props.value.message);
                                this.props.modalControl.close();
                            }}
                        >
                            <img src="/img/icon/ic-close.svg" alt="close" />
                        </div>
                    </div>

                    {/* content */}
                    <div className="content">
                        {this.renderView()}
                    </div>
                </div>

            </div>
        );
    }
}

Modal.defaultProps = {
    view: {},
    value: {},
    keep: false
};

Modal.propTypes = {
    view: PropTypes.string,
    actions: PropTypes.object.isRequired,
    loading: PropTypes.bool.isRequired,
    tokenList: PropTypes.array.isRequired,
    modalControl: PropTypes.object.isRequired,
    value: PropTypes.object,
    keep: PropTypes.bool,
    toastOpen: PropTypes.func.isRequired,
    user: PropTypes.object.isRequired
};

export default Modal;
