import React from 'react';
import PropTypes from 'prop-types';
import { translate } from 'react-i18next';
import { scnToStr, bnHex, tenFill } from '../../utils/format';

class Transaction extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {};
    }

    render()
    {
        const { value, t } = this.props;
        const { type, fromToken, toToken, fee, fromDecimal, toDecimal } = value;
        return (
            <div>
                <div className="modal-title">
                    {type === 'buy' ? `${t('buy')}${toToken.token}` : `${t('sell')}${fromToken.token}`}
                </div>
                <div className="modal-content">
                    <div className="detail">
                        <div>
                            <div>{t('actionMessage')}{type === 'buy' ? `${t('buy')}${toToken.token}` : `${t('sell')}${fromToken.token}`}</div>
                            <div>{tenFill(bnHex(fromToken.amount).toString(), -fromDecimal)} {fromToken.token} : {tenFill(bnHex(toToken.amount).toString(), -toDecimal)} {toToken.token}</div>
                            <div>{t('gas')}  {scnToStr(value.gas)} ETH</div>
                            <div>{t('fee')}  {fee ? `${tenFill(fee, -toDecimal)} ${toToken.token}` : 0}</div>
                        </div>
                        <img src="/img/walletalert.png" alt="wallet" />
                    </div>
                </div>
                <div className="modal-btn">
                    <button className={`submit ${type}`} onClick={() => { value.exec(); }}>
                        {type === 'buy' ? `${t('buy')}${toToken.token}` : `${t('sell')}${fromToken.token}`}
                    </button>
                </div>
            </div>
        );
    }
}

Transaction.defaultProps = {
    t: () => {}
};

Transaction.propTypes = {
    value: PropTypes.object.isRequired,
    t: PropTypes.func
};

export default translate('account', { wait: true })(Transaction);
