import React from 'react';
import PropTypes from 'prop-types';
import './loading.scss';

const Loading = (props) =>
{
    return (
        <div className="c_loading">
            <p>{props.title}</p>
            <div className="loader" />
        </div>
    );
};

Loading.propTypes = {
    title: PropTypes.string.isRequired
};

export default Loading;

