import React from 'react';
import PropTypes from 'prop-types';
import { scaleLinear as d3ScaleLinear, } from 'd3-scale';
import { line as d3Line, area as d3Area } from 'd3-shape';
import { axisBottom as d3AxisBottom, axisRight as d3AxisRight } from 'd3-axis';
import { select as d3Select, mouse as d3Mouse } from 'd3-selection';
import { bisector as d3Bisector } from 'd3-array';
// import equal from 'deep-equal';

import './deepChart.scss';
import { formatNumber, times } from '../../utils/format';
// import { transition as d3Transition } from 'd3-transition';
// import { easeLinear as d3EaseLinear } from 'd3-ease';
class DeepChart extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {};
        this.middle = '';
        this.distance = '';
    }

    componentDidMount()
    {
        const $this = this;
        d3Select(this.graph).on('mousemove', function mouseMoveHandler()
        {
            $this.movePoint(d3Mouse(this), $this.total);
        });
    }

    // shouldComponentUpdate(nextProps)
    // {
    //     if (equal(nextProps, this.props))
    //     {
    //         return false;
    //     }
    //     else
    //     {
    //         return true;
    //     }
    // }

    componentWillUnmount()
    {
        d3Select(this.graph).on('mousemove', null);
    }

    chart(mdl, dis)
    {
        const { width, height, margin } = this.props;
        const w = width - margin.left - margin.right;
        const h = height - margin.bottom - margin.top;

        const xScale = d3ScaleLinear()
            .domain([mdl - dis, mdl + dis])
            .range([0, w]);

        const yScale = d3ScaleLinear()
            .domain([0, this.maxValue * 1.2])
            .range([h, 0]);

        const line = d3Line()
            .x(d => xScale(d.displayPrice))
            .y(d => yScale(d.value));

        const area = d3Area()
            .x(d => xScale(d.displayPrice))
            .y0(h)
            .y1(d => yScale(d.value));

        const xAxis = d3AxisBottom()
            .scale(xScale)
            .ticks(7);

        const yAxis = d3AxisRight()
            .scale(yScale)
            .ticks(5);

        const xGrid = d3AxisBottom()
            .scale(xScale)
            .ticks(7)
            .tickSize(h)
            .tickFormat('');

        const yGrid = d3AxisRight()
            .scale(yScale)
            .ticks(5)
            .tickSize(w)
            .tickFormat('');

        return {
            w,
            h,
            line,
            area,
            xScale,
            yScale,
            xAxis,
            yAxis,
            xGrid,
            yGrid,
        };
    }

    movePoint([mouseX, mouseY], total)
    {
        const { xScale, yScale, line } = this.chart(this.middle, this.distance);
        const { margin } = this.props;
        let x = xScale.invert(mouseX - margin.left);
        if (x < total[0].displayPrice) x = total[0].displayPrice;
        if (x > total[total.length - 1].displayPrice) x = total[total.length - 1].displayPrice;
        const bisect = d3Bisector(d => d.displayPrice).left;
        let pos = bisect(total, x, 0, total.length);
        let matchPoint;

        const detail = (match) =>
        {
            if (match.type === 'buy')
            {
                if (x > this.left[this.left.length - 1].displayPrice)
                {
                    x = this.left[this.left.length - 1].displayPrice;
                }
            }
            if (match.type === 'sell')
            {
                if (x < this.right[0].displayPrice)
                {
                    x = this.right[0].displayPrice;
                }
            }

            if (match.type === 'sell' && match.value === 0)
            {
                match.value = this.right[1].value;
            }

            d3Select(this.circle)
                .attr('cx', xScale(x))
                .attr('cy', yScale(match.value));

            d3Select(this.line)
                .attr('d', line([{ displayPrice: x, value: this.maxValue * 1.2 }, { displayPrice: x, value: 0 }]));

            d3Select(this.t_displayPrice)
                .attr('fill', '#fff');

            d3Select(this.rect)
                .attr('fill', 'rgba(0, 0, 0, 0.6)')
                .attr('stroke', '#E7A977')
                .attr('stroke-width', 2)
                .attr('stroke-opacity', 0.7);

            d3Select(this.info_displayPrice)
                .text(`${formatNumber(7, x)}`);

            d3Select(this.t_amount)
                .attr('fill', '#fff');

            d3Select(this.info_amount)
                .text(`${formatNumber(5, match.value)}`);

            d3Select(this.text)
                .attr('x', xScale(x) - 60)
                .attr('y', yScale(match.value) - 60);

            if (match.type === 'sell')
            {
                d3Select(this.info_displayPrice)
                    .attr('class', 'red');

                d3Select(this.info_amount)
                    .attr('class', 'red');
            }
            else
            {
                d3Select(this.info_displayPrice)
                    .attr('class', 'green');

                d3Select(this.info_amount)
                    .attr('class', 'green');
            }
        };

        const check = (m) =>
        {
            if (m.value === 0)
            {
                if (m.type === 'sell')
                {
                    matchPoint = this.total[this.total.indexOf(m) + 1];
                }
                else
                {
                    matchPoint = this.total[this.total.indexOf(m) - 1];
                }
            }
        };

        if (pos > total.length)
        {
            matchPoint = total[total.length - 1];
            detail(matchPoint);
            return;
        }

        if (pos === 0)
        {
            detail(total[0]);
            return;
        }

        // handle out of right space error
        if (pos === total.length)
        {
            matchPoint = total[pos - 1];
            detail(matchPoint);
        }
        else
        {
            const smaller = total[pos - 1];
            const larger = total[pos];
            matchPoint = x - smaller.displayPrice <= larger.displayPrice - x ? smaller : larger;
            check(matchPoint);
            detail(matchPoint);
        }
    }

    render()
    {
        const { sell, buy, margin } = this.props;
        if (sell.length === 0 && buy.length === 0)
        {
            return (
                <div className="no-result">
                    {/*  */}
                </div>
            );
        }
        const $this = this;
        let sellbook = [];
        let buybook = [];
        this.total = [];
        this.right = [];
        this.left = [];

        if (sell.length > 0)
        {
            sellbook = [...sell].sort((a, b) => a.displayPrice - b.displayPrice).reduce((prev, curr) =>
            {
                let next = prev;
                let node = next.find((order) =>
                {
                    return order.displayPrice === curr.displayPrice;
                });

                if (node)
                {
                    node.amount += curr.amount;
                }
                else
                {
                    next.push(Object.assign({}, curr));
                }
                return next;
            }, []);

            for (let i = 0; i < sellbook.length; i += 1)
            {
                this.right.push(sellbook[i]);
                if (sellbook[i + 1])
                {
                    this.right.push({ ...sellbook[i + 1], value: sellbook[i].value });
                }
            }

            this.right.push({ ...this.right[this.right.length - 1], displayPrice: times(String(this.right[this.right.length - 1].displayPrice), String(1.1)) });
            this.right.unshift({ ...this.right[0], value: 0 });
        }

        if (buy.length > 0)
        {
            const book = [...buy].sort((a, b) => a.displayPrice - b.displayPrice);
            buybook = book.reduce((prev, curr) =>
            {
                const next = prev;
                const node = next.find((order) =>
                {
                    return order.displayPrice === curr.displayPrice;
                });

                if (node)
                {
                    node.amount += curr.amount;
                }
                else
                {
                    next.push(Object.assign({}, curr));
                }
                return next;
            }, []);

            for (let i = 0; i < buybook.length; i += 1)
            {
                this.left.push(buybook[i]);
                if (buybook[i + 1])
                {
                    this.left.push({ ...buybook[i], value: buybook[i + 1].value });
                }
            }
            this.left.unshift({ ...this.left[0], displayPrice: this.left[0].displayPrice * 0.9 });
            this.left.push({ ...this.left[this.left.length - 1], value: 0 });
        }

        this.total = this.left.concat(this.right);
        if (!buy[0])
        {
            this.middle = Number(sell[0].displayPrice);
        }
        else if (!sell[0])
        {
            this.middle = Number(buy[0].displayPrice);
        }
        else
        {
            this.middle = (Number(buy[0].displayPrice) + Number(sell[sell.length - 1].displayPrice)) / 2;
        }
        const max = this.total[this.total.length - 1];
        const min = this.total[0];
        this.distance = max.displayPrice - this.middle > this.middle - min.displayPrice ? max.displayPrice - this.middle : this.middle - min.displayPrice;
        if (min.displayPrice - this.middle <= 0)
        {
            this.distance = this.middle;
            const bk = this.right.indexOf(this.right.find(book => book.displayPrice > (this.middle + this.distance)));
            if (bk > 0)
            {
                this.right.splice(bk + 1, this.right.length - bk);
            }
        }
        if (sellbook.length > 0)
        {
            this.right[this.right.length - 1] = { ...this.right[this.right.length - 1], displayPrice: (this.middle + this.distance), value: this.right[this.right.length - 2].value };
        }

        if (buybook.length > 0)
        {
            this.left[0] = { ...this.left[0], displayPrice: (this.middle - this.distance) };
        }

        this.total = this.left.concat(this.right);

        d3Select(this.graph).on('mousemove', function mouseMoveHandler()
        {
            $this.movePoint(d3Mouse(this), $this.total);
        });

        this.maxValue = this.total[0].value > this.total[this.total.length - 1].value ? this.total[0].value : this.total[this.total.length - 1].value;

        const { w, h, line, area, xAxis, yAxis, xGrid, yGrid } = this.chart(this.middle, this.distance);
        return (
            <svg className="c_deepChart" ref={(svg) => { this.graph = svg; }}>
                <g style={{ transform: `translate(${margin.left}px, ${38 + margin.bottom}px)` }}>
                    <path className="line sell-line" d={line(this.right)} />
                    <path className="area sell-area" d={area(this.right)} />
                    <path className="line buy-line" d={line(this.left)} />
                    <path className="area buy-area" d={area(this.left)} />
                    <path className="line middle-line" d={line([{ displayPrice: this.middle, value: this.maxValue * 1.2 }, { displayPrice: this.middle, value: 0 }])} />
                    <g className="grid" ref={node => d3Select(node).call(yGrid)} />
                    <g className="grid" ref={node => d3Select(node).call(xGrid)} />
                    <g className="xAxis" style={{ transform: `translateY(${h}px)` }} ref={node => d3Select(node).call(xAxis)} />
                    <g className="yAxis" style={{ transform: `translateX(${w}px)` }} ref={node => d3Select(node).call(yAxis)} />
                    <circle cx="-100" ref={(c) => { this.circle = c; }} className="circle" r="5" />
                    <path className="point-line" ref={(p) => { this.line = p; }} />
                    <svg className="detail" ref={(text) => { this.text = text; }} height="50">
                        <rect ref={(r) => { this.rect = r; }} width="120" height="46" fill="transparent" />
                        <text y="3" x="4">
                            <tspan fill="transparent" dy="1.2em" ref={(t) => { this.t_displayPrice = t; }}>Price<tspan ref={(t) => { this.info_displayPrice = t; }} className="info" /></tspan>
                        </text>
                        <text y="23" x="4">
                            <tspan fill="transparent" dy="1.2em" ref={(t) => { this.t_amount = t; }}>Total<tspan ref={(t) => { this.info_amount = t; }} className="info" /></tspan>
                        </text>
                    </svg>
                </g>
            </svg>
        );
    }
}

DeepChart.propTypes = {
    sell: PropTypes.array.isRequired,
    buy: PropTypes.array.isRequired,
    height: PropTypes.number.isRequired,
    width: PropTypes.number.isRequired,
    margin: PropTypes.object.isRequired
};

export default DeepChart;
