import React from 'react';
import PropTypes from 'prop-types';
import update from 'immutability-helper';
import { translate } from 'react-i18next';
import QRCode from 'qrcode.react';
import { observerAllowance } from '../../utils/rpc';
import { bnHex } from '../../utils/format';

class ObserverQr extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            checked: false,
        };

        this.renderBtn = this.renderBtn.bind(this);
        this.submit = this.submit.bind(this);
        this.check = this.check.bind(this);
    }

    componentDidMount()
    {
        this.check();
    }

    shouldComponentUpdate(nextProps, nextState)
    {
        if (nextProps.value.type === this.props.value.type && nextState.checked === this.state.checked)
        {
            return false;
        }
        else
        {
            return true;
        }
    }

    async check()
    {
        const { value, user, actions } = this.props;

        if (value.type === 'cancel')
        {
            this.setState(update(this.state, {
                checked: { $set: true }
            }));
            actions.userActions.changeKeep(false);
            return;
        }

        if (value.fromToken.address === finalConfig.ethAddress)
        {
            this.setState(update(this.state, {
                checked: { $set: true }
            }));
            actions.userActions.changeKeep(false);
            return;
        }

        // gasLimit
        // if (value.type === 'approveZero')
        // {
        //     const pass = await observerAllowance(value.fromToken, user.wallet.address);
        //     if (parseInt(pass, 16) === 0)
        //     {
        //         return this.setState(update(this.state, {
        //             checked: { $set: true }
        //         }));
        //     }
        //     else
        //     {
        //         return setTimeout(() =>
        //         {
        //             this.check();
        //         }, 5000);
        //     }
        // }
        // else
        // {
        //     const pass = await observerCheckApprove(user.wallet.address, value.data);
        //     console.log(pass);
        //     if (value.type === 'approve' && parseInt(pass, 16) > 40000)
        //     {
        //         this.setState(update(this.state, {
        //             checked: { $set: true }
        //         }));
        //     }
        //     else if (value.type !== 'approve' && parseInt(pass, 16) < 40000)
        //     {
        //         this.setState(update(this.state, {
        //             checked: { $set: true }
        //         }));
        //         actions.userActions.changeKeep(false);
        //     }
        //     else
        //     {
        //         setTimeout(() =>
        //         {
        //             this.check();
        //         }, 5000);
        //     }
        // }

        const pass = await observerAllowance(value.fromToken, user.wallet.address);
        if (value.type === 'approveZero' && parseInt(pass, 16) === 0) // approve 0, pass0
        {
            return this.setState(update(this.state, {
                checked: { $set: true }
            }));
        }
        else if (value.type === 'approve' && parseInt(pass, 16) >= Number(bnHex(value.fromToken.amount).toString())) // approve, passapprove
        {
            this.setState(update(this.state, {
                checked: { $set: true }
            }));
        }
        else if (value.type === 'sign' && parseInt(pass, 16) < Number(bnHex(value.fromToken.amount).toString())) // sendRawTransaction, passapprove
        {
            this.setState(update(this.state, {
                checked: { $set: true }
            }));
            actions.userActions.changeKeep(false);
        }
        else
        {
            setTimeout(() =>
            {
                this.check();
            }, 5000);
        }
    }

    submit()
    {
        const { value, modalControl, actions } = this.props;
        if (!this.state.checked) return;
        if (value.type === 'approve' || value.type === 'approveZero')
        {
            value.exec();
            this.setState(update(this.state, {
                checked: { $set: false }
            }));
        }
        else
        {
            actions.userActions.changeKeep(false);
            modalControl.close();
        }
    }

    renderBtn()
    {
        const { checked } = this.state;
        const { value, t } = this.props;
        if (value.type === 'approve' || value.type === 'approveZero')
        {
            return (
                <div className="modal-btn">
                    <div className={checked ? 'button' : 'block'} onClick={this.submit}>
                        <span>
                            {t('next')}
                        </span>
                    </div>
                </div>
            );
        }
        else
        {
            return (
                <div className="modal-btn">
                    <div className={checked ? 'button' : 'block'} onClick={this.submit}>
                        <span>{t('finish')}</span>
                    </div>
                </div>
            );
        }
    }

    render()
    {
        const { t, value } = this.props;
        return (
            <div className="c_observerQr">
                <div className="modal-title">
                    {(value.type === 'approve' || value.type === 'approveZero') ? t('checkBalance') : t('confirmSign')}
                </div>
                <div className="modal-content">
                    <p>{value.type === 'approveZero' ? t('observerFix') : t('observeConfirm')}</p>
                </div>
                <div className="canvas-holder">
                    <div className="qrcode">
                        <QRCode value={value.qrcode} size={200} />
                    </div>
                </div>
                {this.renderBtn()}
            </div>
        );
    }
}

ObserverQr.defaultProps = {
    t: () => {}
};

ObserverQr.propTypes = {
    t: PropTypes.func,
    value: PropTypes.object.isRequired,
    modalControl: PropTypes.object.isRequired,
    user: PropTypes.object.isRequired,
    actions: PropTypes.object.isRequired
};

export default translate('account', { wait: true })(ObserverQr);

