import React from 'react';
import update from 'immutability-helper';
import PropTypes from 'prop-types';

import './toast.scss';

class Toast extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            style: ''
        };
    }

    componentDidMount()
    {
        setTimeout(() =>
        {
            this.setState(update(this.state, {
                style: { $set: 'fadeIn' }
            }));
        }, 1);
    }

    render()
    {
        return (
            <div className={`c_toast ${this.state.style}`}>
                {this.props.message}
            </div>
        );
    }
}

Toast.defaultProps = {
    message: ''
};

Toast.propTypes = {
    message: PropTypes.string
};

export default Toast;
