import React from 'react';
import update from 'immutability-helper';
import PropTypes from 'prop-types';
import { createPortal } from 'react-dom';

import Modal from '../../components/modal/modal';
import Toast from '../../components/toast/toast';
import Loading from '../../components/loading/loading';

class WithPopup extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            modal: false,
            view: '',
            value: '',
            toast: false,
            toastMessage: ''
        };

        this.modalOpen = this.modalOpen.bind(this);
        this.modalClose = this.modalClose.bind(this);
        this.toastOpen = this.toastOpen.bind(this);

        this.modalControl = {
            open: this.modalOpen,
            close: this.modalClose
        };

        this.timeout = null;
    }

    componentWillUnmount()
    {
        this.timeout = null;
    }

    modalOpen(view, value)
    {
        this.setState(update(this.state, {
            modal: { $set: true },
            view: { $set: view },
            value: { $set: value }
        }));
    }

    modalClose()
    {
        this.setState(update(this.state, {
            modal: { $set: false }
        }));
    }

    toastOpen(m)
    {
        this.setState(update(this.state, {
            toast: { $set: true },
            toastMessage: { $set: m }
        }), () =>
        {
            clearTimeout(this.timeout);
            this.timeout = setTimeout(() =>
            {
                if (this.state.toast)
                {
                    this.setState(update(this.state, {
                        toast: { $set: false },
                    }));
                }
            }, 2500);
        });
    }

    renderModal()
    {
        if (this.state.modal)
        {
            return createPortal(
                <Modal
                    view={this.state.view}
                    value={this.state.value}
                    modalControl={this.modalControl}
                    actions={this.props.actions}
                    loading={this.props.loading}
                    tokenList={this.props.tokenList}
                    keep={this.props.keep}
                    toastOpen={this.toastOpen}
                    user={this.props.user}
                />,
                document.getElementsByTagName('body')[0],
            );
        }
        else
        {
            return null;
        }
    }

    renderLoading()
    {
        if (this.props.loading)
        {
            return <Loading title={this.props.message} />;
        }
        else
        {
            return null;
        }
    }

    renderToast()
    {
        if (this.state.toast)
        {
            return createPortal(
                <Toast
                    message={this.state.toastMessage}
                />,
                document.getElementsByTagName('body')[0],
            );
        }
        else
        {
            return null;
        }
    }


    render()
    {
        return (
            <div>
                {this.renderModal()}
                {this.renderToast()}

                {this.props.children(this.modalControl, this.toastOpen)}
                {this.renderLoading()}
            </div>
        );
    }
}

WithPopup.defaultProps = {
    actions: {},
    message: '',
    loading: false,
    keep: false,
    tokenList: [],
    user: {}
};

WithPopup.propTypes = {
    children: PropTypes.func.isRequired,
    actions: PropTypes.object,
    message: PropTypes.string,
    loading: PropTypes.bool,
    keep: PropTypes.bool,
    tokenList: PropTypes.array,
    user: PropTypes.object,
};

export default WithPopup;
