import React from 'react';
import { translate } from 'react-i18next';
import PropTypes from 'prop-types';
import update from 'immutability-helper';

import { loginIO } from '../../utils/login';
import PasswordMeter from '../passwordMeter/passwordMeter';


class CreateWallet extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            password: '',
            passwordType: true // if true, type === 'password'
        };

        this.handleChange = this.handleChange.bind(this);
        this.submitCreate = this.submitCreate.bind(this);
    }

    componentDidMount()
    {
        window.onbeforeunload = (e) =>
        {
            if (this.state.download)
            {
                const dialogText = 'Dialog text here';
                e.returnValue = dialogText;
                return dialogText;
            }
        };
    }

    componentWillUnmount()
    {
        window.onbeforeunload = null;
    }

    handleChange(e)
    {
        this.setState(update(this.state, {
            password: { $set: e.target.value }
        }));
    }

    submitCreate()
    {
        const { actions, modalControl, t } = this.props;
        if (this.state.password.length < 8) return this.props.toastOpen(t('passwordRequired'));

        const create = () =>
        {
            actions.modalActions.updateLoading(true);
            actions.modalActions.updateMessage(this.props.t('creating'));
            const createKey = () =>
            {
                return new Promise((resolve, reject) =>
                {
                    const password = this.state.password;
                    const worker = new Worker('/js/createKeyWorker.js');
                    worker.onmessage = (e) =>
                    {
                        setTimeout(() =>
                        {
                            if (e.data.error) reject(e.data.error);
                            resolve(e.data);
                        }, 300);
                    };
                    worker.postMessage({ password });
                });
            };

            return createKey().then((res) =>
            {
                this.key = res.key;

                const testWallet = { address: 'da5acb19966aff9b4d9cf96800de46f7ffa93db5', crypto: { cipher: 'aes-128-ctr', ciphertext: '093c35acd2fe31abad157d9895a52ab41d9d063ac3060eb89dc50134f7150890', cipherparams: { iv: 'c6cf2f52298b7c565522465d99d5735b' }, mac: 'fba07854d84acc672aedddee1a394115c8b3f6bbc5a4d99b4f1455e32269cea3', kdf: 'scrypt', kdfparams: { dklen: 32, n: 262144, r: 1, p: 8, salt: 'c55af92bf301b96bb67cddb5036b6af98293b724775d1c0675895670bb536d23' } }, id: '2a246859-80fb-4811-892e-0b81e287103d', version: 3 };
                const testKey = '5e1bda1beb2c82e07fd2c493ce9f0b940fcea99fb757cae57f6eb8611604f407';
                const w = !finalConfig.production ? testWallet : '';
                const k = !finalConfig.production ? testKey : '';

                loginIO(res.wallet, res.password, this.key, this.props.tokenList, this.props.actions, w, k).then(() =>
                {
                    if (this.state.password)
                    {
                        this.setState(update(this.state, {
                            password: { $set: '' }
                        }));
                    }

                    actions.modalActions.updateLoading(false);
                    actions.userActions.changeKeep(true);
                    modalControl.open('exportWallet', { wallet: res.wallet, downloadHref: `data:${res.data}`, message: t('notDownloadWallet') });
                });


                // loginIO(res.wallet, res.password, this.key, this.props.tokenList, this.props.actions, w, k).then(() =>
                // {
                //     if (this.state.password)
                //     {
                //         this.setState(update(this.state, {
                //             password: { $set: '' }
                //         }));
                //     }

                //     actions.modalActions.updateLoading(false);
                //     actions.userActions.changeKeep(true);
                //     modalControl.open('exportWallet', { wallet: res.wallet, downloadHref: `data:${res.data}`, message: t('notDownloadWallet') });
                // });
            }).catch(console.log);
        };

        if (!this.state.password)
        {
            return this.props.modalControl.open('checkPassword', { exec: create });
        }
        else
        {
            create();
        }
    }

    render()
    {
        const { passwordType, password } = this.state;
        const { t } = this.props;
        return (
            <div>
                <p className="modal-title">{t('createNew')}</p>
                <div className="modal-content">
                    <div className="modal-input">
                        <label htmlFor="password">
                            {t('setPassword')}
                        </label>
                        <div>
                            <input type={passwordType ? 'password' : 'text'} onChange={this.handleChange} placeholder={t('enterPassword')} disabled={this.props.loading} />
                        </div>
                    </div>
                    <div className="meter">
                        <PasswordMeter password={password} />
                        <p>({t('passwordRequired')})</p>
                    </div>
                    <div>
                        <label htmlFor="show-password">
                            <input type="checkbox" onChange={() => { this.setState(update(this.state, { passwordType: { $set: !this.state.passwordType } })); }} />
                            &nbsp;&nbsp;{t('showPassword')}
                        </label>
                    </div>
                </div>
                <div className="modal-btn">
                    <div className="button" onClick={this.props.modalControl.close}>
                        <span>{t('cancel')}</span>
                    </div>
                    <div className="button" onClick={this.submitCreate}>
                        <span>{t('create')}</span>
                    </div>
                </div>
            </div>
        );
    }
}

CreateWallet.defaultProps = {
    t: () => {}
};

CreateWallet.propTypes = {
    t: PropTypes.func,
    actions: PropTypes.object.isRequired,
    tokenList: PropTypes.array.isRequired,
    modalControl: PropTypes.object.isRequired,
    loading: PropTypes.bool.isRequired,
    toastOpen: PropTypes.func.isRequired
};

export default translate('account', { wait: true })(CreateWallet);
