import React from 'react';
import PropTypes from 'prop-types';
import { translate } from 'react-i18next';
import { scnToStr } from '../../utils/format';

class Offset extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {};
    }

    render()
    {
        const { value, t } = this.props;
        return (
            <div>
                <div className="modal-title">
                    {t('mortgage.offset')}{value.toToken.token}
                </div>
                <div className="modal-content">
                    <div className="detail">
                        <div>
                            <div>{t('gas')}  {scnToStr(value.gas)} ETH</div>
                            <div>{t('fee')}  {`${value.fee} XPA`}</div>
                        </div>
                        <img src="/img/walletalert.png" alt="wallet" />
                    </div>
                </div>
                <div className="modal-btn">
                    <div className="button" onClick={() => { value.exec(); }}>
                        <span>{t('mortgage.offset')}{value.toToken.token}</span>
                    </div>
                </div>
            </div>
        );
    }
}

Offset.defaultProps = {
    t: () => {}
};

Offset.propTypes = {
    value: PropTypes.object.isRequired,
    t: PropTypes.func
};

export default translate('account', { wait: true })(Offset);
