import React from 'react';
import PropTypes from 'prop-types';
import { translate } from 'react-i18next';

class Receipt extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {};
    }

    render()
    {
        const { value, t, modalControl } = this.props;
        return (
            <div>
                <div className="modal-title">
                    {t('sent')}
                </div>
                <div className="modal-content">
                    <p className="receipt-title">{t('txMessage')}</p>
                    <a className="receipt" href={`https://etherscan.io/tx/${value.receipt}`} target="_blank">
                        {value.receipt}
                    </a>
                </div>
                <div className="modal-btn">
                    <div className="button" onClick={() => { modalControl.close(); }}>
                        <span>{t('finish')}</span>
                    </div>
                </div>
            </div>
        );
    }
}

Receipt.defaultProps = {
    t: () => {}
};

Receipt.propTypes = {
    value: PropTypes.object.isRequired,
    t: PropTypes.func,
    modalControl: PropTypes.object.isRequired
};

export default translate('account', { wait: true })(Receipt);
