import React from 'react';
import update from 'immutability-helper';
import PropTypes from 'prop-types';
import { translate } from 'react-i18next';
import * as rpc from '../../utils/rpc';
import './myTransaction.scss';
import {
    formatNumber,
    formatTime,
    bnHex,
    tenFill,
} from '../../utils/format';

class MyTransaction extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            toggleOrder: '',
        };
        this.closeOrder = this.closeOrder.bind(this);
    }

    async closeOrder(item)
    {
        const { myBooks, user, t, modalControl, toastOpen, from, to, modalActions, actions } = this.props;
        if (item.no < 0) return toastOpen(t('table.cancelAlert'));
        const index = myBooks.indexOf(myBooks.find(b => b.no === item.no));
        const fromToken = {
            address: from.token === item.from ? from.address : to.address,
            price: item.price,
            amount: item.amount,
            decimal: from.address === item.from ? from.deciaml : to.decimal
        };
        const toToken = { address: from.token === item.to ? from.address : to.address };

        const tx = await rpc.estimateCancel(fromToken, toToken, user.wallet.address, user.key, '');

        const confirm = async () =>
        {
            modalActions.updateLoading(true);
            modalActions.updateMessage(t('table.canceling'));

            if (user.observer)
            {
                const qrcode = await rpc.cancelStream(tx);
                modalControl.open('observerQr', { type: 'cancel', qrcode });
                modalActions.updateLoading(false);
            }
            else
            {
                actions.userActions.myBooksChange(item.no);
                const canceled = await rpc.cancelOrder(tx);
                if (canceled)
                {
                    this.props.cancelPending(index);
                    modalActions.updateLoading(false);
                    modalControl.open('receipt', { receipt: canceled });
                }
            }
        };
        modalControl.open('cancelOrder', {
            desc: item.desc,
            price: item.displayPrice,
            fill: item.fill,
            amount: item.displayAmount,
            type: item.type,
            exec: confirm,
            gas: tenFill((tx.gasLimit * tx.gasPrice), -18)
        });
    }

    toggleOrder(type)
    {
        if (type !== '')
        {
            this.setState(update(this.state, {
                toggleOrder: { $set: 'close' }
            }));
        }
        else
        {
            this.setState(update(this.state, {
                toggleOrder: { $set: '' }
            }));
        }
    }

    renderThead()
    {
        const { t } = this.props;
        if (this.state.toggleOrder === '')
        {
            return (
                <div className="t-header">
                    <div>{t('table.desc')}</div>
                    <div>{t('table.deal')}/{t('table.amount')}</div>
                    <div>{t('table.ratio')}</div>
                </div>
            );
        }
        else
        {
            return (
                <div className="t-header">
                    <div>{t('table.desc')}</div>
                    <div>{t('table.amount')}</div>
                    <div>{t('table.ratio')}</div>
                </div>
            );
        }
    }

    renderBody()
    {
        // 
        const { myBooks, t, from } = this.props;
        if (this.state.toggleOrder === '')
        {
            const data = myBooks.map((item) =>
            {
                return (
                    <div className={!item.cancel ? 'mt-list' : 'mt-list block'} key={item.no}>
                        <div>
                            <div className="info">
                                <div className="desc">
                                    <div>
                                        <span className={item.type === 'buy' ? 'buy' : 'sell'}>{/buy/.test(item.type) ? 'B' : 'S'}</span>
                                        <span>
                                            <span className="from">{item.desc.split('/')[0]}</span><span className="base">/{item.desc.split('/')[1]}</span>
                                        </span>
                                    </div>
                                    <div className="time">{typeof item.timestamp === 'number' ? formatTime(item.timestamp, 'date') : item.timestamp}</div>
                                </div>
                                <div className="item">
                                    <div>{formatNumber(8, tenFill(bnHex(item.fill).toString(), -from.decimal))}</div>
                                    <div>{formatNumber(8, item.displayAmount)}</div>
                                </div>
                                <div className="price">{formatNumber(6, item.displayPrice)}</div>
                                <div className="cancel">
                                    <img src="/img/icon/ic-del.svg" alt="cancel" onClick={() => { this.closeOrder(item); }} />
                                </div>
                            </div>
                        </div>
                    </div>
                );
            });

            if (data.length === 0)
            {
                return (
                    <div className="no-result" key="1">{t('table.noHistory')}</div>
                );
            }
            return data;
        }
        else // 
        {
            const data = this.props.myHistory.map((item, i) =>
            {
                const style = (type) =>
                {
                    switch (type)
                    {
                        case 'cbuy':
                            return 'buy';
                        case 'csell':
                            return 'sell';
                        case 'cancel':
                            return 'cancel';
                        case 'tbuy':
                            return 'buy';
                        case 'tsell':
                            return 'sell';
                        default:
                            return 'trade';
                    }
                };
                if (item.type === 'take')
                {
                    return null;
                }
                else
                {
                    return (
                        <a href={`https://etherscan.io/tx/0x${item.no.split('0x')[2]}`} target="_blank" key={item.hash || i}>
                            <div className={/c/.test(item.type) ? 'mt-list canceled' : 'mt-list'}>
                                <div>
                                    <div className="info">
                                        <div className="desc">
                                            <div>
                                                <span className={style(item.type)}>{/buy/.test(item.type) ? 'B' : 'S'}</span>
                                                <span>
                                                    <span className="from"> {item.desc.split('/')[0]}</span><span className="base">/{item.desc.split('/')[1]}</span>
                                                </span>
                                            </div>
                                            <div className="time">{typeof item.timestamp === 'number' ? formatTime(item.timestamp, 'date') : item.timestamp}</div>
                                        </div>
                                        <div className="item">
                                            <div>{formatNumber(8, item.amount)}</div>
                                        </div>
                                        <div className="price">
                                            {formatNumber(10, item.displayPrice)}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </a>

                    );
                }
            });

            if (data.length === 0)
            {
                return (
                    <div className="no-result" key="1">{t('table.noHistory')}</div>
                );
            }
            return data;
        }
    }

    render()
    {
        const { t, myBooks, myHistory } = this.props;

        return (
            <div className="c_myTransaction">
                <div className="title">
                    {t('table.pendingOrder')}
                </div>
                <div className="order">
                    <div className={this.state.toggleOrder === '' ? 'active' : ''} onClick={() => this.toggleOrder('')}>
                        {t('table.pendingOrder')}
                        <span className="volume">{myBooks.length}</span>
                    </div>
                    <div className={this.state.toggleOrder !== '' ? 'active' : ''} onClick={() => this.toggleOrder('close')}>
                        {t('table.closed')}
                        <span className="volume">{myHistory.filter(item => item.type !== 'take').length}</span>
                    </div>
                </div>
                <div className="table">
                    {this.renderThead()}
                    <div className="t-body">{this.renderBody()}</div>
                </div>
            </div>
        );
    }
}

MyTransaction.defaultProps = {
    t: () => {}
};

MyTransaction.propTypes = {
    myBooks: PropTypes.array.isRequired,
    cancelPending: PropTypes.func.isRequired,
    user: PropTypes.object.isRequired,
    from: PropTypes.object.isRequired,
    to: PropTypes.object.isRequired,
    t: PropTypes.func,
    modalControl: PropTypes.object.isRequired,
    toastOpen: PropTypes.func.isRequired,
    myHistory: PropTypes.array.isRequired,
    modalActions: PropTypes.object.isRequired,
    actions: PropTypes.object.isRequired
};

export default translate()(MyTransaction);
