/* eslint-disable no-undef */
import React from 'react';
import PropTypes from 'prop-types';
import { translate } from 'react-i18next';
import equal from 'deep-equal';
import { bnHex, bnTen, tenFill, formatNumber } from '../../utils/format';
import * as rpc from '../../utils/rpc';
import './marketTransaction.scss';


class MarketTransaction extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {};

        this.openMarketTrans = this.openMarketTrans.bind(this);
        this.immTrans = this.immTrans.bind(this);
        this.enterMarket = this.enterMarket.bind(this);
        this.clickFocus = this.clickFocus.bind(this);

        this.timeout = null;
        this.length = 0;
    }

    componentDidMount()
    {
        window.addEventListener('click', this.clickOutside);
    }

    shouldComponentUpdate(nextProps, nextState)
    {
        if (equal(nextProps, this.props) && equal(nextState, this.state))
        {
            return false;
        }
        else
        {
            return true;
        }
    }

    componentWillUnmount()
    {
        window.removeEventListener('click', this.clickOutside);
        this.timeout = null;
    }

    clickFocus(e)
    {
        const t = e.currentTarget.dataset.type;
        this.amount.focus();
        this.props.handleFocus(t);
    }

    enterMarket(e)
    {
        const value = e.target.value;

        this.props.handleInput('amount', value);
        clearTimeout(this.timeout);
        this.timeout = setTimeout(() =>
        {
            const {
                sellbook,
                buybook,
                actions,
                from,
                to,
                toastOpen,
                t,
                myTransaction,
                handlePreview,
                amount
            } = this.props;

            const { myTransactionChange } = actions.userActions;
            const disable = () =>
            {
                myTransactionChange({
                    price: '',
                    amount: '',
                    able: false,
                    act: myTransaction.act,
                    total: '-'
                });
            };

            if (value < (10 ** -6))
            {
                disable();
                return toastOpen(`${from.token} ${t('min')}${10 ** -6}`);
            }
            else if (value > (10 ** 9))
            {
                disable();
                return toastOpen(`${from.token} ${t('max')}${10 ** 9}`);
            }

            let total;
            if (myTransaction.act === 'buy')
            {
                const sellArray = [...sellbook].reverse();
                const p = sellArray.find(d => d.value >= value);
                if (p)
                {
                    const price = bnTen(tenFill(1, 36)).div(bnHex(p.price)).toString(16);
                    const tmpAmount = bnTen(tenFill(value, from.decimal)).toString(16);

                    total = bnTen(tenFill(bnHex(tmpAmount).toString(), 18)).div(bnHex(price)).toString(16);
                    this.length = sellArray.indexOf(p) + 1;
                    myTransactionChange({
                        price,
                        amount: tmpAmount,
                        able: true,
                        act: myTransaction.act,
                        total
                    });
                }
                else
                {
                    disable();
                }
            }
            else
            {
                // act = 'sell'
                // find buybook
                const p = buybook.find(d => d.value >= value);
                if (p)
                {
                    const price = bnTen(tenFill(1, 36)).div(bnHex(p.price)).toString(16);
                    const tmpAmount = bnTen(tenFill(value, from.decimal)).toString(16);
                    total = bnTen(tenFill(bnHex(tmpAmount).mul(bnHex(price)).toString(), -18).split('.')[0]).toString(16);
                    this.length = buybook.indexOf(p) + 1;

                    myTransactionChange({
                        price,
                        amount: tmpAmount,
                        able: true,
                        act: myTransaction.act,
                        total
                    });
                }
                else
                {
                    disable();
                }
            }

            handlePreview(amount.value, tenFill(bnHex(total).toString(), -to.decimal));
        }, 500);
    }

    async immTrans(type, tx)
    {
        const { user, actions, createQuery, t, toastOpen, handleInput } = this.props;
        const { myTransactionChange } = actions.userActions;

        actions.modalActions.updateLoading(true);
        actions.modalActions.updateMessage(user.observer ? t('dataGenerating') : t('success'));

        if (user.observer)
        {
            rpc.createStream(tx).then((res) =>
            {
                // console.log(res);
                actions.modalActions.updateLoading(false);
                if (tx.skip)
                {
                    res.approve = '';
                }
                createQuery(type, res.raw, res.approve, tx.from, tx.data);
            });
        }
        else
        {
            const receipt = await rpc.transaction(tx);
            if (receipt)
            {
                this.props.modalControl.open('receipt', { receipt });
                actions.modalActions.updateLoading(false);
            }
            else
            {
                toastOpen(t('notSent'));
                actions.modalActions.updateLoading(false);
            }
        }

        this.length = 0;
        handleInput('amount', '');
        myTransactionChange({
            price: '',
            amount: '',
            able: false,
            total: '-',
            act: type
        });
    }

    async openMarketTrans()
    {
        const {
            from,
            to,
            tokenList,
            myTransaction,
            account,
            t,
            user,
            actions,
            toastOpen,
            amount
        } = this.props;
        const agent = '';
        let fromToken;
        let toToken;

        if (!user.wallet) return toastOpen(`${t('importAlert')}`);
        if (amount.value > (10 ** 9)) return toastOpen(`${from.token}${t('max')}${10 ** 9}`);
        if (amount.value < (10 ** -2)) return toastOpen(`${from.token}${t('min')}${10 ** -2}`);
        if (tenFill(bnHex(myTransaction.total).toString(), -to.decimal) > (10 ** 9)) return toastOpen(`Total ${to.token}${t('max')}${10 ** 9}`);
        if (tenFill(bnHex(myTransaction.total).toString(), -to.decimal) < (10 ** -2)) return toastOpen(`Total ${to.token}${t('min')}${10 ** -2}`);

        const fToken = tokenList.find(tk => tk.address === from.address);
        const tToken = tokenList.find(tk => tk.address === to.address);

        if (myTransaction.act === 'sell')
        {
            const target = account.find(l => l.address === from.address);
            const a = bnHex(target.usable);
            const b = bnTen(tenFill(amount.value, from.decimal));
            if (a.lt(b)) return toastOpen(`${from.token}${t('out')}`);

            fromToken = {
                token: fToken.token,
                address: fToken.address,
                price: myTransaction.price,
                amount: myTransaction.amount,
            };

            toToken = {
                token: tToken.token,
                address: tToken.address,
                amount: myTransaction.total,
            };
        }
        else
        {
            const target = account.find(l => l.address === to.address);
            const a = bnHex(target.usable);
            const b = bnHex(myTransaction.total);
            if (a.lt(b)) return toastOpen(`${to.token}${t('out')}`);

            fromToken = {
                token: tToken.token,
                address: tToken.address,
                price: myTransaction.price,
                amount: myTransaction.total,
            };

            toToken = {
                token: fToken.token,
                address: fToken.address,
                amount: myTransaction.amount,
            };
        }

        actions.modalActions.updateLoading(true);
        actions.modalActions.updateMessage(t('gettingTx'));

        const checkETH = (gas) =>
        {
            const eth = account.find(d => d.address === finalConfig.ethAddress);
            let fromBalance = Number(tenFill(bnHex(eth.usable).toString(), -18));
            if (fromToken.address === finalConfig.ethAddress)
            {
                fromBalance -= tenFill(bnHex(fromToken.amount).toString(), -to.decimal);
            }

            if (fromBalance < Number(gas))
            {
                return false;
            }
            else
            {
                return true;
            }
        };


        let fee = 0;
        if (this.length > 0)
        {
            fee = await rpc.calcFee(user.wallet.address, toToken);
        }

        if (!user.observer)
        {
            const tx = await rpc.estimateGas(fromToken, toToken, user.wallet.address, user.key, agent, true);

            if (tx)
            {
                actions.modalActions.updateLoading(false);
                const gas = tx.userAddress ? ((tx.gasPrice * tx.gasLimit) / (10 ** 18)) + ((this.length + 1) * (1500000 / (10 ** 18))) + (50000 / (10 ** 18)) : (tx.gasPrice * tx.gasLimit) / (10 ** 18);
                if (!checkETH(gas))
                {
                    return toastOpen(`ETH${t('out')}`);
                }

                this.props.modalControl.open('transaction', {
                    type: myTransaction.act,
                    fromToken,
                    toToken,
                    gas,
                    fee,
                    fromDecimal: tx.fromDecimal,
                    toDecimal: tx.toDecimal,
                    exec: () =>
                    {
                        this.immTrans(myTransaction.act, tx);
                    }
                });
            }
            else
            {
                actions.modalActions.updateLoading(false);
                toastOpen(t('failMessage'));
                console.warn('Error');
            }
        }
        else
        {
            actions.userActions.changeKeep(true);
            let allowance = await rpc.observerAllowance(fromToken, user.wallet.address);
            actions.modalActions.updateLoading(false);

            const estimate = async (skip) =>
            {
                const tx = await rpc.observerEstimatGas(fromToken, toToken, user.wallet.address, agent, true);
                if (skip)
                {
                    tx.skip = true;
                }

                this.immTrans(myTransaction.act, tx);
            };

            if (allowance === '0x') allowance = '0';

            if (bnHex(allowance.replace('0x', '')).lt(bnHex(fromToken.amount)) && (allowance / (10 ** 18)) !== 0)
            {
                const pass = await rpc.observerApprove({ ...fromToken, amount: '0' });
                if (pass)
                {
                    // estimate();
                    this.props.modalControl.open('observerQr', { type: 'approve', qrcode: pass, exec: () => { this.props.modalControl.close(); estimate(); }, fromToken, message: '' });
                }
            }
            else if (bnHex(allowance.replace('0x', '')).gte(bnHex(fromToken.amount)))
            {
                estimate(true);
            }
            else
            {
                this.props.modalControl.close();
                estimate();
            }
        }
    }

    renderButton()
    {
        const { from, t, mouseEnterPending, mouseLeavePending, myTransaction } = this.props;
        const { act, able } = myTransaction;
        return (
            <div className="submit">
                <button
                    className={act === 'buy' ? 'green' : 'red'}
                    onClick={this.openMarketTrans}
                    onMouseEnter={() => mouseEnterPending(act)}
                    onMouseLeave={() => mouseLeavePending()}
                    disabled={!able}
                >{t(act)} {from.token}
                </button>
            </div>
        );
    }

    render()
    {
        const { from, to, t, myTransaction, amount } = this.props;

        return (
            <div className="c_marketTransaction">
                <div className="input-field">
                    <div>{t('amount')}</div>
                    <div className={amount.active ? 'enter active' : 'enter'} data-type="amount" onClick={this.clickFocus}>
                        <input
                            type="text"
                            onChange={this.enterMarket}
                            value={amount.value}
                            ref={(input) =>
                            {
                                this.amount = input;
                            }}
                        />
                        <div className="type from">{from.token}</div>
                    </div>
                </div>

                <div className="submit-field">
                    <div className="volume">
                        <div>TOTAL</div>
                        <div>
                            <div className="total"> {myTransaction.total === '-' ? '-' : formatNumber(8, tenFill(bnHex(myTransaction.total).toString(), -to.decimal))}</div>
                            <div>{to.token}</div>
                        </div>
                    </div>
                    {this.renderButton()}
                </div>
            </div>
        );
    }
}

MarketTransaction.defaultProps = {
    t: () => {}
};

MarketTransaction.propTypes = {
    from: PropTypes.object.isRequired,
    to: PropTypes.object.isRequired,
    account: PropTypes.array.isRequired,
    user: PropTypes.object.isRequired,
    tokenList: PropTypes.array.isRequired,
    buybook: PropTypes.array.isRequired,
    sellbook: PropTypes.array.isRequired,
    t: PropTypes.func,
    myTransaction: PropTypes.object.isRequired,
    actions: PropTypes.object.isRequired,
    mouseEnterPending: PropTypes.func.isRequired,
    mouseLeavePending: PropTypes.func.isRequired,
    createQuery: PropTypes.func.isRequired,
    handlePreview: PropTypes.func.isRequired,
    modalControl: PropTypes.object.isRequired,
    toastOpen: PropTypes.func.isRequired,
    amount: PropTypes.object.isRequired,
    handleFocus: PropTypes.func.isRequired,
    handleInput: PropTypes.func.isRequired
};

export default translate('account', { wait: true })(MarketTransaction);
