import React from 'react';
import { translate } from 'react-i18next';
import PropTypes from 'prop-types';
import QRCode from 'qrcode.react';

class ExportWallet extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {};
    }

    componentDidMount()
    {
        window.onbeforeunload = (e) =>
        {
            if (this.state.download)
            {
                const dialogText = 'Dialog text here';
                e.returnValue = dialogText;
                return dialogText;
            }
        };
    }

    componentWillUnmount()
    {
        window.onbeforeunload = null;
    }

    render()
    {
        const { t, value } = this.props;

        return (
            <div>
                <p className="modal-title">{t('createNew')}</p>
                <div className="modal-content">
                    <div>
                        <div>
                            {t('yourWallet')}
                        </div>
                        <div>
                            {`0x${value.wallet.address.replace('0x', '')}`}
                        </div>
                    </div>
                    <div className="canvas">
                        <div className="qrcode">
                            <QRCode value={JSON.stringify(value.wallet)} size={120} />
                        </div>
                    </div>
                </div>
                <div className="modal-btn">
                    <a
                        href={value.downloadHref}
                        download="keyStore.json"
                        className="button margin-none"
                        target={/iPad|iPhone|iPod/.test(navigator.userAgent) && !window.MSStream ? '_blank' : ''}
                        onClick={() => { this.props.modalControl.close(); this.props.actions.userActions.changeKeep(false); }}
                    >
                        <span>
                            {t('downloadWallet')}
                        </span>
                    </a>
                </div>
            </div>
        );
    }
}

ExportWallet.defaultProps = {
    t: () => {}
};

ExportWallet.propTypes = {
    t: PropTypes.func,
    actions: PropTypes.object.isRequired,
    modalControl: PropTypes.object.isRequired,
    value: PropTypes.object.isRequired,
};

export default translate('account', { wait: true })(ExportWallet);
