import React from 'react';
import PropTypes from 'prop-types';
import { translate } from 'react-i18next';
import zxcvbn from 'zxcvbn';
import './passwordMeter.scss';

class PasswordMeter extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {};
    }

    render()
    {
        const { password } = this.props;
        const style = () =>
        {
            if (password === '') return 'none';
            switch (zxcvbn(password).score)
            {
                case 0:
                    return 'risk';
                case 1:
                    return 'weak';
                case 2:
                    return 'normal';
                case 3:
                    return 'safly';
                case 4:
                    return 'strong';
                default:
                    return 'none';
            }
        };

        return (
            <div className="c_password-meter">
                <div className={style()} />
            </div>
        );
    }
}

PasswordMeter.defaultProps = {
    t: () => {}
};

PasswordMeter.propTypes = {
    password: PropTypes.string.isRequired
};

export default translate()(PasswordMeter);
