import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import update from 'immutability-helper';
import { translate } from 'react-i18next';

import Header from '../../components/header/header';
import Content from '../../components/content/content';
import Account from '../../components/account/account';
import RecentTransaction from '../../components/recentTransaction/recentTransaction';
import Orders from '../../components/orders/orders';
import MyTransaction from '../../components/myTransaction/myTransaction';
import Footer from '../../components/footer/footer';
import TideWallet from '../../components/tideWallet/tideWallet';
import connectFirebase from '../../hoc/connectFirebase';
import WithPopup from '../../components/withPopup/withPopup';
import Tab from '../../components/tab/tab';
import TokenList from '../../components/tokenList/tokenList';
import MortgageLink from '../../components/mortgageLink/mortgageLink';

import * as bookActions from '../../actions/book';
import * as userActions from '../../actions/user';
import * as modalActions from '../../actions/modal';
import * as newsActions from '../../actions/news';
import * as mortgageActions from '../../actions/mortgage';

import './index.scss';

function mapStateToProps(state)
{
    return {
        from: state.book.from,
        to: state.book.to,
        sell: state.book.sell,
        buy: state.book.buy,
        tokenList: state.book.tokenList,
        summary: state.book.summary,
        tradeList: state.book.tradeList,
        transaction: state.book.transaction,
        kData: state.book.kLine,
        myBooks: state.user.myBooks,
        account: state.user.account,
        user: state.user.user,
        myTransaction: state.user.myTransaction,
        keep: state.user.keep,
        loading: state.modal.loading,
        message: state.modal.message,
        news: state.news,
        myHistory: state.user.myHistory
    };
}

function mapDispatchToProps(dispatch)
{
    return {
        actions: {
            bookActions: bindActionCreators(bookActions, dispatch),
            userActions: bindActionCreators(userActions, dispatch),
            modalActions: bindActionCreators(modalActions, dispatch),
            newsActions: bindActionCreators(newsActions, dispatch),
            mortgageActions: bindActionCreators(mortgageActions, dispatch)
        }
    };
}

class Index extends React.Component
{
    constructor()
    {
        super();
        this.state = {
            user: {},
            nav: 1,
        };

        this.cancelPending = this.cancelPending.bind(this);
        this.select = this.select.bind(this);
        this.changeNav = this.changeNav.bind(this);

        this.modalControl = {
            open: this.modalOpen,
            close: this.modalClose
        };
    }

    //  from, to
    select(obj)
    {
        const { from, to, tokenList, tradeList, user, actions, myTransaction } = this.props;
        let f = from;
        let t = to;

        if (obj)
        {
            const { type, baseAddress, id, decimal } = obj;
            const address = tradeList[type].find(d => d.id === id).address;
            const re = tokenList.find(d => d.address === address);
            t = { token: type, address: baseAddress, decimal };
            f = { token: re.token, address: re.address, decimal: re.decimal };
        }

        actions.bookActions.changeFrom(f);
        actions.bookActions.changeTo(t);

        // 
        actions.modalActions.updateLoading(true);
        this.props.actions.bookActions.bookGet$();
        // window.dbObj.ref('orderBooks').once('value', (snapshot) =>
        // {
        //     actions.bookActions.bookGet$(Object.values(snapshot.val()));
        //     if (user.wallet)
        //     {
        //         actions.userActions.myBooksGet$(snapshot);
        //     }
        // });

        window.dbObj.ref('priceBooks').once('value', (snapshot) =>
        {
            actions.bookActions.recentTransactionGet$(Object.values(snapshot.val()));
        });

        if (user.wallet)
        {
            window.dbObj.ref('operatorBooks').orderByChild('user').equalTo(`0x${user.wallet.address}`).once('value', (snapshot) =>
            {
                userActions.historyGet$(Object.values(snapshot.val() || []));
            });
        }

        //  myTransaction
        userActions.myTransactionChange({
            price: '',
            amount: '',
            able: false,
            total: '-',
            act: myTransaction.act
        });

        this.child.changeInput('', '');
    }

    // ,  Nav
    changeNav(type)
    {
        // const chart = document.querySelector('.chart');
        // if (chart && type !== 1)
        // {
        //     chart.style.display = 'none';
        // }
        // else if (chart && type === 1)
        // {
        //     chart.style.display = 'flex';
        // }
        this.setState(update(this.state, {
            nav: { $set: type }
        }));
    }

    // 
    cancelPending(index)
    {
        const { t, actions, myBooks } = this.props;
        if (!myBooks[index]) return;

        //  pending , 
        if (myBooks[index].no < 0) return this.toastOpen(t('table.cancelAlert'));
        actions.userActions.myBooksCancel(index);
    }

    render()
    {
        const {
            from,
            to,
            sell,
            buy,
            account,
            actions,
            myBooks,
            user,
            myTransaction,
            tokenList,
            tradeList,
            transaction,
            kData,
            loading,
            myHistory
        } = this.props;

        const { nav } = this.state;

        // const tokenData = data.find(d => d.address.toLowerCase() === from.address.toLowerCase());

        return (
            <WithPopup
                actions={this.props.actions}
                loading={this.props.loading}
                tokenList={this.props.tokenList}
                keep={this.props.keep}
                message={this.props.message}
                user={user}
            >
                {(modal, toastOpen) => (
                    <div className="co_index">
                        <Header
                            from={from}
                            to={to}
                            dash={true}
                            tradeList={tradeList}
                            actions={actions}
                            tokenList={tokenList}
                            select={this.select}
                            myTransaction={myTransaction}
                        />
                        <main>
                            <TokenList tradeList={tradeList} select={this.select} actions={actions} tokenList={tokenList} />
                            <Tab switch={(type) =>
                            {
                                this.changeNav(type);
                            }}
                            />
                            <div className={nav === 2 ? 'sidebar rwd-show' : 'sidebar'}>
                                <Account
                                    account={account}
                                    from={from}
                                    to={to}
                                    sellbook={sell}
                                    buybook={buy}
                                    tokenList={tokenList}
                                    myTransaction={myTransaction}
                                    actions={actions}
                                    user={user}
                                    loading={loading}
                                    modalControl={modal}
                                    toastOpen={toastOpen}
                                    onRef={(ref) =>
                                    {
                                        this.child = ref;
                                    }}
                                />
                                <MyTransaction
                                    myBooks={myBooks}
                                    cancelPending={this.cancelPending}
                                    user={user}
                                    tokenList={tokenList}
                                    from={from}
                                    to={to}
                                    myHistory={myHistory}
                                    modalControl={modal}
                                    toastOpen={toastOpen}
                                    modalActions={actions.modalActions}
                                    actions={actions}
                                />

                                <TideWallet />
                            </div>
                            <div className={nav > 0 ? 'content rwd-show' : 'content'}>
                                <div className={nav === 1 ? 'chart rwd-show' : 'chart'} >
                                    <Content
                                        sell={sell}
                                        buy={buy}
                                        data={kData}
                                        from={from}
                                        to={to}
                                        tokenList={tokenList}
                                        actions={actions}
                                    />
                                </div>
                                <div className={nav === 3 ? 'transaction rwd-show' : 'transaction'} >
                                    <section className={nav === 2 ? 'rwd-show' : ''} >
                                        <Orders
                                            from={from}
                                            to={to}
                                            sell={sell}
                                            buy={buy}
                                            actions={actions}
                                            myTransaction={myTransaction}
                                            toastOpen={toastOpen}
                                            changePrice={(v) =>
                                            {
                                                this.child.changeInput(v, null);
                                            }}
                                        />
                                    </section>
                                    <section className={nav === 3 ? 'rwd-show' : ''} >
                                        <RecentTransaction transaction={transaction} from={from} to={to} />
                                    </section>
                                </div>
                                <div className="transaction">
                                    <section>
                                        <MortgageLink />
                                    </section>
                                    <section />
                                </div>
                            </div>
                        </main>
                        <Footer />
                    </div>
                )}
            </WithPopup>
        );
    }
}

Index.defaultProps = {
    t: () => {},
};

Index.propTypes = {
    from: PropTypes.object.isRequired,
    to: PropTypes.object.isRequired,
    sell: PropTypes.array.isRequired,
    buy: PropTypes.array.isRequired,
    tokenList: PropTypes.array.isRequired,
    tradeList: PropTypes.object.isRequired,
    transaction: PropTypes.array.isRequired,
    kData: PropTypes.array.isRequired,
    account: PropTypes.array.isRequired,
    myBooks: PropTypes.array.isRequired,
    actions: PropTypes.object.isRequired,
    user: PropTypes.object.isRequired,
    myTransaction: PropTypes.object.isRequired,
    keep: PropTypes.bool.isRequired,
    t: PropTypes.func,
    loading: PropTypes.bool.isRequired,
    message: PropTypes.string.isRequired,
    myHistory: PropTypes.array.isRequired,
};

export default connect(mapStateToProps, mapDispatchToProps)(connectFirebase(translate()(Index)));
