/* eslint-disable no-undef */
import React from 'react';
import PropTypes from 'prop-types';
import update from 'immutability-helper';
import { translate } from 'react-i18next';
import equal from 'deep-equal';
import { tenFill, bnTen, bnHex, formatNumber } from '../../utils/format';
import * as rpc from '../../utils/rpc';
import './limitTransaction.scss';


class LimitTransaction extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            tmp: -1, // 
        };

        this.handleInput = this.handleInput.bind(this);
        this.openLimitPending = this.openLimitPending.bind(this);
        this.pending = this.pending.bind(this);
        this.clickFocus = this.clickFocus.bind(this);
        this.renderButton = this.renderButton.bind(this);

        this.timeout = null;
    }

    componentDidMount()
    {
        window.addEventListener('click', this.clickOutside);
    }

    shouldComponentUpdate(nextProps, nextState)
    {
        if (equal(nextProps, this.props) && equal(nextState, this.state))
        {
            return false;
        }
        else
        {
            return true;
        }
    }

    componentWillUnmount()
    {
        window.removeEventListener('click', this.clickOutside);
        this.timeout = null;
    }

    // div inpufocus
    clickFocus(e)
    {
        const type = e.currentTarget.dataset.input;

        this[type].focus();
        this.props.handleFocus(type);
    }

    handleInput(e)
    {
        const name = e.target.name;
        const value = e.target.value;
        this.props.handleInput(name, value);
        // 500
        clearTimeout(this.timeout);
        this.timeout = setTimeout(() =>
        {
            const { from, to, t, toastOpen, handlePreview, actions, myTransaction, price, amount } = this.props;
            const { myTransactionUpdate } = actions.userActions;

            const disable = () =>
            {
                myTransactionUpdate({ type: 'able', value: false });
                myTransactionUpdate({ type: 'total', value: '-' });
            };

            //  10 ** 9
            if (amount.value > 0 && amount.value > (10 ** 9))
            {
                disable();
                return toastOpen(`${from.token} ${t('max')}${10 ** 9}`);
            }
            //  10 ** -2
            else if (amount.value && amount.value < (10 ** -2))
            {
                disable();
                return toastOpen(`${from.token} ${t('min')}${10 ** -2}`);
            }
            // 
            else if (Number(amount.value))
            {
                myTransactionUpdate({
                    type: 'amount',
                    value: bnTen(tenFill(amount.value, from.decimal)).toString(16)
                });
            }
            else
            {
                myTransactionUpdate({
                    type: 'amount',
                    value: ''
                });
            }

            if (price.value && price.value > 0)
            {
                const buyPrice = () =>
                {
                    let tmp = tenFill(bnTen(tenFill(1, 36)).div(bnTen(tenFill(price.value, 18))).toString(), from.decimal - to.decimal);
                    return bnTen(tmp.split('.')[0]).toString(16);
                };
                myTransactionUpdate({
                    type: 'price',
                    value: myTransaction.act === 'sell' ? bnTen(tenFill(price.value, (18 + to.decimal) - from.decimal)).toString(16) : buyPrice(),
                });
            }
            else if (name === 'price')
            {
                myTransactionUpdate({
                    type: 'price',
                    value: ''
                });
            }
            if (Number(amount.value) && Number(price.value))
            {
                const tmpPrice = tenFill(price.value, 18);
                const tmpAmount = tenFill(amount.value, from.decimal);
                const tv = tenFill(bnTen(tmpAmount).mul(bnTen(tmpPrice)).toString(), -18 - from.decimal);
                if (tv >= (10 ** 9)) //  10 ** 9
                {
                    disable();
                    return toastOpen(`Total ${to.token} ${t('max')}1000000000`);
                }
                else if (tv < (10 ** -2)) //  10 ** -2
                {
                    disable();
                    return toastOpen(`Total ${to.token} ${t('min')}0.01`);
                }
                else
                {
                    const hexTotal = bnTen(tenFill(tv, to.decimal)).toString(16);
                    myTransactionUpdate({ type: 'total', value: hexTotal });
                    myTransactionUpdate({ type: 'able', value: true });
                    handlePreview(amount.value, tv);
                }
            }
            else
            {
                disable();
            }
        }, 500);
    }

    // 
    async openLimitPending()
    {
        const {
            account,
            to,
            from,
            t,
            user,
            sellbook,
            buybook,
            tokenList,
            actions,
            toastOpen,
            myTransaction,
            amount
        } = this.props;

        const agent = '';

        if (!user.wallet) return toastOpen(`${t('importAlert')}`);
        if (myTransaction.act === 'sell')
        {
            const target = account.find(l => l.address === from.address);
            const a = bnHex(target.usable);
            const b = bnTen((amount.value * (10 ** from.decimal)).toFixed(0));
            if (a.lt(b)) return toastOpen(`${from.token}${t('out')}`);
        }
        else
        {
            const target = account.find(l => l.address === to.address);
            const a = bnHex(target.usable);
            const b = bnHex(myTransaction.total);
            if (a.lt(b)) return toastOpen(`${to.token}${t('out')}`);
        }


        // 
        const fToken = tokenList.find(l => l.address === from.address);
        const tToken = tokenList.find(l => l.address === to.address);
        let fromToken;
        let toToken;

        let arr = [];
        let long = null;

        // 
        if (myTransaction.act === 'buy')
        {
            long = 0;
            const tmpSellBook = [...sellbook].reverse();
            const p = bnTen(tenFill(1, 36)).div(bnHex(myTransaction.price));
            for (let i = 0; i < tmpSellBook.length; i += 1)
            {
                if (bnHex(tmpSellBook[i].price).lte(p) || Number(bnHex(tmpSellBook[i].price).toString()) - Number(p.toString()) < 10 ** 7)
                {
                    long += 1;
                    break;
                }
            }

            if (long > 0)
            {
                arr = tmpSellBook.slice(0, long);
            }
            fromToken = {
                token: tToken.token,
                address: tToken.address,
                amount: myTransaction.total,
                price: myTransaction.price,
            };
            toToken = {
                token: fToken.token,
                address: fToken.address,
                amount: myTransaction.amount,
            };
        }
        else
        {
            for (let i = 0; i < buybook.length; i += 1)
            {
                if (bnHex(buybook[i].price).lte(bnTen(tenFill(1, 36)).div(bnHex(myTransaction.price))))
                {
                    long = i + 1;
                }
            }

            if (long)
            {
                arr = buybook.slice(0, long);
            }

            fromToken = {
                token: fToken.token,
                address: fToken.address,
                amount: myTransaction.amount,
                price: myTransaction.price,
            };
            toToken = {
                token: tToken.token,
                address: tToken.address,
                amount: myTransaction.total,
            };
        }

        actions.modalActions.updateLoading(true);
        actions.modalActions.updateMessage(t('gettingTx'));

        let fee;
        if (arr.length > 0)
        {
            fee = await rpc.calcFee(user.wallet.address, toToken);
        }
        else
        {
            fee = 0;
        }

        const checkETH = (gas) =>
        {
            const eth = account.find(d => d.address === finalConfig.ethAddress);
            let fromBalance = Number(tenFill(bnHex(eth.usable).toString(), -18));
            if (fromToken.address === finalConfig.ethAddress)
            {
                fromBalance -= tenFill(bnHex(fromToken.amount).toString(), -to.decimal);
            }

            if (fromBalance < Number(gas))
            {
                return false;
            }
            else
            {
                return true;
            }
        };

        if (!user.observer)
        {
            const tx = await rpc.estimateGas(fromToken, toToken, user.wallet.address, user.key, agent);

            if (tx)
            {
                actions.modalActions.updateLoading(false);
                const gas = tx.userAddress ? Number(tenFill(tx.gasPrice * tx.gasLimit, -18)) + ((arr.length + 1) * Number(tenFill(1500000, -18))) + Number(tenFill(50000, -18)) : tenFill(tx.gasPrice * tx.gasLimit, -18);

                if (!checkETH(gas))
                {
                    return toastOpen(`ETH${t('out')}`);
                }

                this.props.modalControl.open('transaction', {
                    type: myTransaction.act,
                    fromToken,
                    toToken,
                    fromDecimal: tx.fromDecimal,
                    toDecimal: tx.toDecimal,
                    gas,
                    fee,
                    exec: () =>
                    {
                        this.pending(myTransaction.act, tx);
                    }
                });
            }
            else
            {
                actions.modalActions.updateLoading(false);
                toastOpen(t('failMessage'));
                console.warn('Error');
            }
        }
        else
        {
            actions.userActions.changeKeep(true);
            let allowance = await rpc.observerAllowance(fromToken, user.wallet.address);
            actions.modalActions.updateLoading(false);

            const estimate = async (skip) =>
            {
                const tx = await rpc.observerEstimatGas(fromToken, toToken, user.wallet.address, agent);
                if (skip)
                {
                    tx.skip = true;
                }
                this.pending(myTransaction.act, tx);
            };

            if (allowance === '0x') allowance = '0';
            if (bnHex(allowance.replace('0x', '')).lt(bnHex(fromToken.amount)) && (allowance / (10 ** 18)) !== 0)
            {
                const pass = await rpc.observerApprove({ ...fromToken, amount: '0' });
                if (pass)
                {
                    // estimate();
                    this.props.modalControl.open('observerQr', { type: 'approveZero', qrcode: pass, exec: () => { this.props.modalControl.close(); estimate(); }, fromToken, message: t('observerWaiting') });
                }
            }
            else if (bnHex(allowance.replace('0x', '')).gte(bnHex(fromToken.amount)))
            {
                estimate(true);
            }
            else
            {
                this.props.modalControl.close();
                estimate();
            }
        }
    }

    async pending(type, tx)
    {
        const { from, to, t, user, createQuery, toastOpen, actions, price, amount, handleInput } = this.props;
        const { myBooksAdd, myTransactionChange } = actions.userActions;
        // 
        let book = {
            no: this.state.tmp,
            desc: `${to.token}/${from.token}`,
            status: 'active',
            timestamp: t('sending'),
            displayPrice: price.value,
            type,
            fill: 0,
            amount: amount.value,
            from: to.token,
            to: from.token
        };

        actions.modalActions.updateLoading(true);
        actions.modalActions.updateMessage(user.observer ? t('dataGenerating') : t('success'));
        // if (type === 'sell')
        // {
        //     book = { ...book, from: to.token, to: from.token, amount: Number(this.from.value) };
        // }
        // else
        // {
        //     book = { ...book, from: from.token, to: to.token, amount: Number(volume) };
        // }

        // 
        if (user.observer)
        {
            rpc.createStream(tx).then((res) =>
            {
                if (tx.skip)
                {
                    res.approve = '';
                }
                createQuery(type, res.raw, res.approve, tx.from, tx.data);
                actions.modalActions.updateLoading(false);
            });
        }
        else
        {
            //  -1 
            this.setState(update(this.state, {
                tmp: { $set: this.state.tmp - 1 }
            }), () =>
            {
                myBooksAdd(book);
            });

            const receipt = await rpc.transaction(tx);

            if (receipt)
            {
                actions.modalActions.updateLoading(false);
                // toastOpen(t('sent'));
                this.props.modalControl.open('receipt', { receipt });
            }
            else
            {
                toastOpen(t('notSent'));
                actions.modalActions.updateLoading(false);
            }
        }

        // input
        handleInput('price', '');
        handleInput('amount', '');
        myTransactionChange({
            price: '',
            amount: '',
            able: false,
            total: '-',
            act: type
        });
    }

    renderButton()
    {
        const { t, from, mouseEnterPending, mouseLeavePending, myTransaction } = this.props;

        return (
            <div className="submit">
                <button
                    className={myTransaction.act === 'buy' ? 'green' : 'red'}
                    onClick={this.openLimitPending}
                    onMouseEnter={() => mouseEnterPending(myTransaction.act)}
                    onMouseLeave={() => mouseLeavePending()}
                    disabled={!myTransaction.able}
                >{t(myTransaction.act)} {from.token}
                </button>
            </div>
        );
    }

    render()
    {
        const { from, to, t, myTransaction, price, amount } = this.props;

        return (
            <div className="c_limitTransaction">
                <div className="input-field">
                    <div>{t('price')}</div>
                    <div className={price.active ? 'enter active' : 'enter'} data-input="price" onClick={this.clickFocus}>
                        <input
                            id="price"
                            type="text"
                            name="price"
                            onChange={this.handleInput}
                            value={price.value}
                            ref={(input) =>
                            {
                                this.price = input;
                            }}
                        />
                        <div className="type">{to.token}</div>
                    </div>
                </div>

                <div className="input-field">
                    <div>{t('amount')}</div>
                    <div className={amount.active ? 'enter active' : 'enter'} data-input="amount" onClick={this.clickFocus}>
                        <input
                            id="amount"
                            type="text"
                            name="amount"
                            onChange={this.handleInput}
                            value={amount.value}
                            ref={(input) =>
                            {
                                this.amount = input;
                            }}
                        />
                        <div className="type from">{from.token}</div>
                    </div>
                </div>

                <div className="submit-field">
                    <div className="volume">
                        <div>TOTAL</div>
                        <div>
                            <div className="total">{myTransaction.total !== '-' ? formatNumber(8, tenFill(bnHex(myTransaction.total, 16).toString(), -to.decimal)) : '-'}</div>
                            <div>{to.token}</div>
                        </div>
                    </div>
                    {this.renderButton()}
                </div>
            </div>
        );
    }
}

LimitTransaction.defaultProps = {
    t: () => {}
};

LimitTransaction.propTypes = {
    from: PropTypes.object.isRequired,
    to: PropTypes.object.isRequired,
    account: PropTypes.array.isRequired,
    user: PropTypes.object.isRequired,
    tokenList: PropTypes.array.isRequired,
    buybook: PropTypes.array.isRequired,
    sellbook: PropTypes.array.isRequired,
    t: PropTypes.func,
    myTransaction: PropTypes.object.isRequired,
    actions: PropTypes.object.isRequired,
    mouseEnterPending: PropTypes.func.isRequired,
    mouseLeavePending: PropTypes.func.isRequired,
    createQuery: PropTypes.func.isRequired,
    handlePreview: PropTypes.func.isRequired,
    modalControl: PropTypes.object.isRequired,
    toastOpen: PropTypes.func.isRequired,
    price: PropTypes.object.isRequired,
    amount: PropTypes.object.isRequired,
    handleInput: PropTypes.func.isRequired,
    handleFocus: PropTypes.func.isRequired
};

export default translate('account', { wait: true })(LimitTransaction);
