import React from 'react';
import PropTypes from 'prop-types';
import { translate } from 'react-i18next';
import { scnToStr, formatNumber } from '../../utils/format';

class CancelOrder extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {};
    }

    render()
    {
        const { t, value } = this.props;
        return (
            <div className="c_cancelOrder">
                <div className="modal-title">
                    {t('table.cancelOrder')}
                </div>
                <div className="modal-content">
                    <p className="cancel-item">{`${t('table.desc')}: ${value.desc}`}</p>
                    <p className="cancel-item">{`${t('table.price')}: ${value.price}`}</p>
                    <p className="cancel-item">{`${t('table.amount')}: ${formatNumber(8, value.amount)}`}</p>
                    <p className="cancel-item">{t('table.gas')}  {scnToStr(value.gas)} ETH</p>
                </div>
                <div className="modal-btn">
                    <div className="button" onClick={value.exec}>
                        <span>
                            {t('table.confirm')}
                        </span>
                    </div>
                </div>
            </div>
        );
    }
}

CancelOrder.defaultProps = {
    t: () => {}
};

CancelOrder.propTypes = {
    t: PropTypes.func,
    value: PropTypes.object.isRequired,
    // modalControl: PropTypes.object.isRequired
};

export default translate()(CancelOrder);
