import React from 'react';
import PropTypes from 'prop-types';
import update from 'immutability-helper';
import { translate } from 'react-i18next';
import equal from 'deep-equal';
import './account.scss';
import { formatNumber, bnHex, bnTen, tenFill } from '../../utils/format';
import Login from '../login/login';
import LimitTransaction from '../limitTransaction/limitTransaction';
import MarketTransaction from '../marketTransaction/marketTransaction';

class Account extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            pendingType: 1, // 1 =  | 2 = 
            preview: { add: '', less: '' }, //  token 
            toggle: false,
            calc: { add: '', less: '' }, // preview 
            price: {
                active: false,
                value: ''
            },
            amount: {
                active: false,
                value: ''
            }
        };

        this.btn = '';

        this.selectPending = this.selectPending.bind(this);
        this.mouseEnterPending = this.mouseEnterPending.bind(this);
        this.mouseLeavePending = this.mouseLeavePending.bind(this);
        this.createQuery = this.createQuery.bind(this);
        this.toggleSelect = this.toggleSelect.bind(this);
        this.changeSelect = this.changeSelect.bind(this);
        this.clickOutside = this.clickOutside.bind(this);
        this.handlePreview = this.handlePreview.bind(this);
        this.copyAddress = this.copyAddress.bind(this);
        this.preview = this.preview.bind(this);
        this.handleInput = this.handleInput.bind(this);
        this.handleFocus = this.handleFocus.bind(this);
    }

    componentDidMount()
    {
        window.addEventListener('click', this.clickOutside);
        this.props.onRef(this);
    }

    shouldComponentUpdate(nextProps, nextState)
    {
        if (equal(nextProps, this.props) && equal(nextState, this.state))
        {
            return false;
        }
        else
        {
            return true;
        }
    }

    componentWillUnmount()
    {
        window.removeEventListener('click', this.clickOutside);
        this.props.onRef(null);
    }


    //  containers/index.js 
    changeInput(p, a)
    {
        this.setState(update(this.state, {
            price: { value: { $set: p } },
            amount: { value: { $set: a === null ? this.state.amount.value : '' } }
        }));
    }

    //  toggle  toggle
    clickOutside(e)
    {
        const toggle = document.querySelector('.c_account .toggle');
        const select = document.querySelector('.c_account .select');
        if (toggle && !select.contains(e.target))
        {
            this.setState(update(this.state, {
                toggle: { $set: false }
            }));
        }

        const active = document.querySelector('.enter.active');
        if (active && !active.contains(e.target))
        {
            // active.className = 'enter';
            this.setState(update(this.state, {
                price: {
                    active: { $set: false }
                },
                amount: {
                    active: { $set: false }
                }
            }));
        }
    }

    changeSelect(e)
    {
        e.stopPropagation();
        const type = e.target.dataset.type;
        // toggle
        if (this.state.toggle === false)
        {
            this.setState(update(this.state, {
                toggle: { $set: true }
            }));
        }
        else
        {
            const { myTransaction, actions, from, to } = this.props;
            const { pendingType } = this.state;

            // 
            if (pendingType === 1 && myTransaction.act !== type && myTransaction.price)
            {
                const { price } = this.state;
                actions.userActions.myTransactionUpdate({
                    type: 'price',
                    value: type === 'sell' ?
                        bnTen(tenFill(price.value, (18 + to.decimal) - from.decimal)).toString(16) :
                        bnTen(tenFill(bnTen(tenFill(1, 36)).div(bnTen(tenFill(price.value, 18))).toString(), from.decimal - to.decimal)).toString(16)
                });
            }
            else if (pendingType !== 1) // 
            {
                actions.userActions.myTransactionChange({
                    price: '',
                    amount: '',
                    total: '-',
                    able: false,
                    act: type
                });
            }
            this.setState(update(this.state, {
                toggle: { $set: false },
                amount: { value: { $set: pendingType !== 1 ? '' : this.state.amount.value } }
            }));

            actions.userActions.myTransactionUpdate({
                type: 'act',
                value: type
            });
        }
    }

    handleFocus(type)
    {
        this.setState(update(this.state, {
            price: {
                active: { $set: type === 'price' ? true : false }
            },
            amount: {
                active: { $set: type === 'price' ? false : true }
            }
        }));
    }

    handleInput(name, value)
    {
        // 8
        if (value.split('.')[1] && value.split('.')[1].length > 8)
        {
            if (Number(value))
            {
                this.setState(update(this.state, {
                    [name]: {
                        value: { $set: Number(value) === 0 ? '' : Number(value).toFixed(9).substr(0, value.length - 1) }
                    }
                }));
            }
        }
        else
        {
            this.setState(update(this.state, {
                [name]: {
                    value: { $set: value }
                }
            }));
        }
    }

    copyAddress()
    {
        document.querySelector('.wallet-address textarea').select();
        document.execCommand('copy');
        this.props.toastOpen(this.props.t('copied'));
    }

    //  tidewallet connectqrcode
    createQuery(type, raw, approve, fromToken, data)
    {
        const { modalControl, actions, t } = this.props;

        //  approve  approve 
        if (approve)
        {
            const exec = () =>
            {
                setTimeout(() =>
                {
                    modalControl.close();
                    modalControl.open('observerQr', { type: 'sign', qrcode: raw, fromToken, message: t('observerWaiting'), data });
                }, 100);
            };
            modalControl.open('observerQr', { type: 'approve', qrcode: approve, exec, fromToken, message: t('observerWaiting'), data });
            actions.modalActions.updateLoading(false);
        }
        else
        {
            //  approve 
            modalControl.open('observerQr', { type: 'sign', qrcode: raw, fromToken, message: t('observerWaiting'), data });
            actions.modalActions.updateLoading(false);
        }
    }

    mouseEnterPending(button)
    {
        const { from, to, myTransaction } = this.props;
        const { able } = myTransaction;
        // const { pendingType } = this.state;
        this.btn = button;

        // if (pendingType === 1)
        // {
        //     if (button === 'buy' && able)
        //     {
        //         this.setState(update(this.state, {
        //             preview: {
        //                 add: { $set: from.token },
        //                 less: { $set: to.token }
        //             }
        //         }));
        //     }
        //     else if (button === 'sell' && able)
        //     {
        //         this.setState(update(this.state, {
        //             preview: {
        //                 add: { $set: to.token },
        //                 less: { $set: from.token }
        //             }
        //         }));
        //     }
        // }

        // to from 
        if (button === 'sell' && able)
        {
            this.setState(update(this.state, {
                preview: {
                    add: { $set: to.token },
                    less: { $set: from.token }
                }
            }));
        }
        // from to 
        else if (button === 'buy' && able)
        {
            this.setState(update(this.state, {
                preview: {
                    add: { $set: from.token },
                    less: { $set: to.token }
                }
            }));
        }
    }

    mouseLeavePending()
    {
        this.setState(update(this.state, {
            preview: {
                add: { $set: '' },
                less: { $set: '' }
            }
        }));
        this.btn = '';
    }

    handlePreview(add, less)
    {
        this.setState(update(this.state, {
            calc: {
                add: { $set: add },
                less: { $set: less }
            }
        }));
    }

    logout()
    {
        window.location.reload();
        // const { t, actions } = this.props;
        // const { setUser, updateMyBooks } = actions.userActions;
        // setUser({});
        // updateMyBooks([]);
        // clearTimeInterval();
        // actions.mortgageActions.mortgageLogout();
        // actions.userActions.updateHistory([]);
        // setTimeout(() =>
        // {
        //     this.props.toastOpen(t('logoutSuccess'));
        // }, 300);
    }

    preview(data)
    {
        let style = '';
        let value = '';
        const { preview, pendingType, calc } = this.state;

        if (data.token === preview.add)
        {
            style = 'add';
            try
            {
                if (pendingType === 1)
                {
                    if (this.btn === 'buy')
                    {
                        value = calc.add;
                    }
                    else
                    {
                        value = calc.less;
                    }
                }
                else if (pendingType !== 1 && this.btn === 'sell')
                {
                    value = calc.less;
                }
                else
                {
                    value = calc.add;
                }

                value = `+${formatNumber(7, Number(value))}`;
            }
            catch (err)
            {
                style = '';
                value = '';
            }
        }
        else if (data.token === preview.less)
        {
            style = 'less';
            try
            {
                if (pendingType === 1)
                {
                    if (this.btn === 'buy')
                    {
                        value = calc.less;
                    }
                    else
                    {
                        value = calc.add;
                    }
                }
                else if (this.btn === 'buy')
                {
                    value = calc.less;
                }
                else
                {
                    value = calc.add;
                }

                value = `-${formatNumber(7, Number(value))}`;
            }
            catch (err)
            {
                style = '';
                value = '';
            }
        }

        return { style, value };
    }

    selectPending(e)
    {
        const { actions, myTransaction } = this.props;
        const { myTransactionChange } = actions.userActions;

        this.setState(update(this.state, {
            pendingType: { $set: Number(e.target.dataset.type) },
            price: { value: { $set: '' } },
            amount: { value: { $set: '' } }
        }));
        myTransactionChange({
            amount: '',
            total: '-',
            able: false,
            price: '',
            act: myTransaction.act
        });
    }

    toggleSelect()
    {
        this.setState(update(this.state, {
            toggle: { $set: !this.state.toggle },
        }));
    }

    renderAccount()
    {
        const {
            user, actions, tokenList, t, loading, modalControl
        } = this.props;

        if (!user.wallet)
        {
            return (
                <Login
                    wallet={user.wallet || {}}
                    actions={actions}
                    tokenList={tokenList}
                    loading={loading}
                    modalControl={modalControl}
                    exportLink={this.exportLink}
                />
            );
        }
        else
        {
            const data = `data:text/json;charset=utf-8,${encodeURIComponent(JSON.stringify(user.wallet))}`;
            return (
                <div className="table">
                    <div className="t-header">
                        <span>{t('balance')}</span>
                    </div>

                    {this.renderTable()}

                    <div className="wallet-address">
                        <div className="address">
                            <div className="wallet-title">
                                {t('yourWallet')}
                            </div>
                            <div>
                                <textarea value={user.wallet ? `0x${user.wallet.address.replace('0x', '')}` : ''} readOnly />
                                <img src="/img/icon/ic-copy.svg" alt="copy" onClick={this.copyAddress} />
                            </div>
                        </div>
                        {!user.observer ?
                            <div className="wallet-export">
                                <a
                                    className="button"
                                    href={data}
                                    download="keyStore.json"
                                    target={/iPad|iPhone|iPod/.test(navigator.userAgent) && !window.MSStream ? '_blank' : ''}
                                >
                                    <span>{t('downloadWallet')}</span>
                                </a>
                            </div>
                            : ''}
                    </div>
                </div>
            );
        }
    }

    renderTable()
    {
        let content;
        const { from, to, account, tokenList } = this.props;

        if (account && account.length > 0)
        {
            content = account.map((data, index) =>
            {
                // fromto
                if (data.address !== from.address && data.address !== to.address)
                {
                    const unit = tokenList.find(d => d.address === data.address).decimal;
                    const usable = tenFill(bnHex(data.usable).toString(), -unit);
                    return (
                        <div key={data.index || index}>
                            <span>{data.token}</span>
                            <span>{usable}</span>
                            <span />
                        </div>
                    );
                }
                else
                {
                    return null;
                }
            });

            // form to
            const top = (param) =>
            {
                const item = account.find(data => data.address === param);
                const pre = this.preview(item);

                const unit = tokenList.find(d => d.address === param).decimal;
                const usable = tenFill(bnHex(item.usable).toString(), -unit);

                return (
                    <div key={item.address} className={param === to.address ? 'active' : ''}>
                        <span>{item.token}</span>
                        <span>{usable}</span>
                        <span className={pre.style}>{pre.value}</span>
                    </div>
                );
            };

            return (
                <div className="t-body">
                    {top(to.address)}
                    {top(from.address)}
                    {content}
                </div>
            );
        }

        return null;
    }

    renderToggle()
    {
        const { from, t } = this.props;

        return (
            <div className={this.state.toggle ? 'toggle' : 'toggle close'}>
                <div data-type="buy" onClick={this.changeSelect}>{t('buy')}{from.token}</div>
                <div data-type="sell" onClick={this.changeSelect}>{t('sell')}{from.token}</div>
            </div>
        );
    }

    renderTransaction()
    {
        const {
            from, to, myTransaction, actions, user, account, tokenList, sellbook, buybook, modalControl, toastOpen
        } = this.props;
        const { price, amount, pendingType } = this.state;

        if (pendingType === 1)
        {
            return (
                <LimitTransaction
                    from={from}
                    to={to}
                    myTransaction={myTransaction}
                    user={user}
                    account={account}
                    tokenList={tokenList}
                    buybook={buybook}
                    sellbook={sellbook}
                    actions={actions}
                    mouseEnterPending={this.mouseEnterPending}
                    mouseLeavePending={this.mouseLeavePending}
                    createQuery={this.createQuery}
                    handlePreview={this.handlePreview}
                    modalControl={modalControl}
                    toastOpen={toastOpen}
                    price={price}
                    amount={amount}
                    handleInput={this.handleInput}
                    handleFocus={(type) =>
                    {
                        this.handleFocus(type);
                    }}
                />
            );
        }
        else
        {
            return (
                <MarketTransaction
                    from={from}
                    to={to}
                    myTransaction={myTransaction}
                    user={user}
                    account={account}
                    tokenList={tokenList}
                    buybook={buybook}
                    sellbook={sellbook}
                    actions={actions}
                    mouseEnterPending={this.mouseEnterPending}
                    mouseLeavePending={this.mouseLeavePending}
                    createQuery={this.createQuery}
                    handlePreview={this.handlePreview}
                    modalControl={modalControl}
                    toastOpen={toastOpen}
                    amount={amount}
                    handleInput={this.handleInput}
                    handleFocus={(type) =>
                    {
                        this.handleFocus(type);
                    }}
                />
            );
        }
    }

    render()
    {
        const { pendingType } = this.state;
        const { t, to, from, user, myTransaction } = this.props;

        return (
            <div className={user.wallet ? 'c_account login' : 'c_account'}>
                <div className="title">{t('transaction')}
                    {user.wallet ?
                        <div className="logout">
                            <span onClick={this.logout}>{t('logoutWallet')}</span>
                        </div>
                        : null
                    }
                </div>

                {this.renderAccount()}

                <div className="buySell">
                    <div className="pending-type">
                        <a className={pendingType === 1 ? 'active' : ''} data-type="1" onClick={this.selectPending}>{t('pendingTrade')}</a>
                        <a className={pendingType === 2 ? 'active' : ''} data-type="2" onClick={this.selectPending}>{t('instantTrade')}</a>
                    </div>

                    <div className="buy-sell">
                        <div className="select" onClick={this.toggleSelect}>
                            <span>{myTransaction.act === 'buy' ? `${t('buy')}${from.token}` : `${t('sell')}${from.token}`}</span>
                            <img src="/img/icon/ic-dropdown-p.svg" alt="dropdown" />

                            {this.renderToggle()}

                        </div>

                        <div className="store">
                            <img src="/img/icon/ic-wallet.svg" alt="wallet" />
                            <span>
                                <span>-</span>
                                {myTransaction.act === 'buy' ? to.token : from.token}
                            </span>
                        </div>
                    </div>

                    <div>
                        {this.renderTransaction()}
                    </div>
                </div>
            </div>
        );
    }
}

Account.defaultProps = {
    t: () => {}
};

Account.propTypes = {
    from: PropTypes.object.isRequired,
    to: PropTypes.object.isRequired,
    account: PropTypes.array.isRequired,
    tokenList: PropTypes.array.isRequired,
    sellbook: PropTypes.array.isRequired,
    buybook: PropTypes.array.isRequired,
    myTransaction: PropTypes.object.isRequired,
    actions: PropTypes.object.isRequired,
    t: PropTypes.func,
    user: PropTypes.object.isRequired,
    loading: PropTypes.bool.isRequired,
    modalControl: PropTypes.object.isRequired,
    toastOpen: PropTypes.func.isRequired,
    onRef: PropTypes.func.isRequired
};

export default translate('account', { wait: true })(Account);
