import React from 'react';
import PropTypes from 'prop-types';
import update from 'immutability-helper';
import { translate } from 'react-i18next';

import { loginIO } from '../../utils/login';

class Observer extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            address: '',
            message: ''
        };

        this.handleChange = this.handleChange.bind(this);
        this.submit = this.submit.bind(this);
    }

    handleChange(e)
    {
        this.setState(update(this.state, {
            address: { $set: e.target.value },
            message: { $set: '' }
        }));
    }

    submit()
    {
        const { t, actions, tokenList, modalControl } = this.props;
        const { address } = this.state;

        if (address === '') return;

        const regex = /^0x[a-fA-F0-9]{40}$/;
        const regexShort = /[a-fA-F0-9]{40}$/;

        if (!regex.test(address) && !regexShort.test(address))
        {
            this.setState(update(this.state, {
                message: { $set: t('addressFormatError') }
            }));
        }
        else
        {
            const value = address.replace('0x', '');
            const wallet = { address: value.toLowerCase() };
            loginIO(wallet, '', '', tokenList, actions).then(() =>
            {
                modalControl.close();
            });
        }
    }

    render()
    {
        const { t } = this.props;
        const { message } = this.state;

        return (
            <div className="c_observer">
                <div className="modal-title">
                    {t('enterAddress')}
                </div>
                <div className="modal-content">
                    <div>{t('observeEnter')}</div>
                    <div className="modal-input">
                        <label htmlFor="address">{t('enterAddress')}</label>
                        <div className={message === '' ? '' : 'error'} data-message={message} >
                            <input id="address" type="text" value={this.state.address} onChange={this.handleChange} />
                        </div>
                    </div>
                </div>
                <div className="modal-btn">
                    <div className="button" onClick={this.submit}>
                        <span>
                            {t('importAddress')}
                        </span>
                    </div>
                </div>
            </div>
        );
    }
}

Observer.defaultProp = {
    t: () => {}
};

Observer.propTypes = {
    t: PropTypes.func.isRequired,
    actions: PropTypes.object.isRequired,
    modalControl: PropTypes.object.isRequired,
    tokenList: PropTypes.array.isRequired
};

export default translate('account', { wait: true })(Observer);
