import React from 'react';
import { translate } from 'react-i18next';
import PropTypes from 'prop-types';

class CheckPassword extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {};

        this.create = this.create.bind(this);
    }

    create()
    {
        this.props.value.exec();
    }

    render()
    {
        const { t } = this.props;

        return (
            <div>
                <div className="modal-content">
                    <p className="warning">{t('creatWithoutPassWord')}</p>
                </div>
                <div className="modal-btn">
                    <div className="button" onClick={() => { this.props.modalControl.open('create'); }}>
                        <span>{t('cancel')}</span>
                    </div>
                    <div className="button" onClick={this.create}>
                        <span>{t('create')}</span>
                    </div>
                </div>
            </div>
        );
    }
}

CheckPassword.defaultProps = {
    t: () => {}
};

CheckPassword.propTypes = {
    t: PropTypes.func,
    modalControl: PropTypes.object.isRequired,
    value: PropTypes.object.isRequired
};

export default translate('account', { wait: true })(CheckPassword);
