import React from 'react';
import PropTypes from 'prop-types';
import update from 'immutability-helper';
import equal from 'deep-equal';
import { translate } from 'react-i18next';
import { Link } from 'react-router-dom';
import OrderBook from '../orderBook/orderBook';
import './orders.scss';

class Order extends React.Component
{
    constructor(props)
    {
        super(props);

        this.state = {
            toggle: false,
            pendingFilter: 8,
            sell: props.sell,
            buy: props.buy
        };

        this.toggleSelect = this.toggleSelect.bind(this);
        this.clickOutside = this.clickOutside.bind(this);
        this.fliter = [8, 7, 6, 5];
    }

    componentDidMount()
    {
        window.addEventListener('click', this.clickOutside);
    }

    // componentWillReceiveProps()
    // {
    //     this.setState(update(this.state, {
    //         sell: { $set: this.props.sell },
    //         buy: { $set: this.props.buy },
    //         pendingFilter: { $set: 8 }
    //     }));
    // }

    componentWillReceiveProps(nextProps)
    {
        const sell = [];
        const buy = [];

        nextProps.sell.forEach((order) =>
        {
            sell.push({ ...order, displayPrice: Number(order.displayPrice).toFixed(this.state.pendingFilter) });
        });

        nextProps.buy.forEach((order) =>
        {
            buy.push({ ...order, displayPrice: Number(order.displayPrice).toFixed(this.state.pendingFilter) });
        });

        this.setState(update(this.state, {
            sell: { $set: sell },
            buy: { $set: buy },
        }));
    }


    // shouldComponentUpdate(nextProps, nextState)
    // {
    //     if (equal(nextProps, this.props) && equal(nextState, this.state))
    //     {
    //         return false;
    //     }
    //     else
    //     {
    //         return true;
    //     }
    // }
    shouldComponentUpdate(nextProps, nextState)
    {
        if (
            !equal(nextState, this.state) ||
            !equal(nextProps.from, this.props.from) ||
            !equal(nextProps.to, this.props.to) ||
            !equal(nextProps.buy, this.props.buy) ||
            !equal(nextProps.sell, this.props.sell) ||
            !equal(nextProps.myTransaction, this.props.myTransaction) ||
            !equal(nextProps.t, this.props.t)
        )
        {
            return true;
        }
        else
        {
            return false;
        }
    }

    componentWillUnmount()
    {
        window.removeEventListener('click', this.clickOutside);
    }

    clickOutside(e)
    {
        if (this.state.toggle === true)
        {
            if (!this.select.contains(e.target))
            {
                this.setState(update(this.state, {
                    toggle: { $set: false }
                }));
            }
        }
    }

    changeSelect(value)
    {
        const sell = [];
        const buy = [];

        this.props.sell.forEach((order) =>
        {
            sell.push({ ...order, displayPrice: Number(order.displayPrice).toFixed(value) });
        });

        this.props.buy.forEach((order) =>
        {
            buy.push({ ...order, displayPrice: Number(order.displayPrice).toFixed(value) });
        });

        this.setState(update(this.state, {
            sell: { $set: sell },
            buy: { $set: buy },
            pendingFilter: { $set: value },
            toggle: { $set: false }
        }));
    }

    toggleSelect()
    {
        this.setState(update(this.state, {
            toggle: { $set: !this.state.toggle },
        }));
    }

    render()
    {
        const { t, from, to, actions, myTransaction, changePrice, toastOpen } = this.props;
        const { pendingFilter, toggle, sell, buy } = this.state;
        sell.forEach((v) =>
        {
            if (typeof v.displayPrice === 'number')
            {
                v.displayPrice = v.displayPrice.toFixed(pendingFilter);
            }
        });

        buy.forEach((v) =>
        {
            if (typeof v.displayPrice === 'number')
            {
                v.displayPrice = v.displayPrice.toFixed(pendingFilter);
            }
        });

        return (
            <div className="c_orders">
                <div className="title">{t('table.pendingOrders')}</div>
                <div className="books-header">
                    <div>{t('table.price')}({to.token})</div>
                    <div>{t('table.amount')}({from.token})</div>
                    <div>{t('table.total')}({from.token})</div>
                </div>
                <OrderBook book={sell} type="sell" actions={actions} myTransaction={myTransaction} from={from} to={to} changePrice={changePrice} toastOpen={toastOpen} />
                <div className="divid" />
                <OrderBook book={buy} type="buy" actions={actions} myTransaction={myTransaction} from={from} to={to} changePrice={changePrice} toastOpen={toastOpen} />
                <div className="filter">
                    <div className="choose" onClick={this.toggleSelect} ref={(button) => { this.select = button; }}>
                        <span>{t('table.decimal', { des: pendingFilter })}</span>
                        <img src="/img/icon/ic-dropdown-p.svg" alt="dropdown" />
                        <div className={toggle ? 'toggle' : 'toggle close'}>
                            <div onClick={(e) => { e.stopPropagation(); this.changeSelect(this.fliter[0]); }}>{this.fliter[0]}</div>
                            <div onClick={(e) => { e.stopPropagation(); this.changeSelect(this.fliter[1]); }}>{this.fliter[1]}</div>
                            <div onClick={(e) => { e.stopPropagation(); this.changeSelect(this.fliter[2]); }}>{this.fliter[2]}</div>
                            <div onClick={(e) => { e.stopPropagation(); this.changeSelect(this.fliter[3]); }}>{this.fliter[3]}</div>
                        </div>
                    </div>
                    <span className="more"><Link to="/order">{t('table.more')}</Link></span>
                </div>
            </div>
        );
    }
}

Order.defaultProps = {
    t: () => {},
    changePrice: () => {},
};

Order.propTypes = {
    from: PropTypes.object.isRequired,
    to: PropTypes.object.isRequired,
    t: PropTypes.func,
    sell: PropTypes.array.isRequired,
    buy: PropTypes.array.isRequired,
    actions: PropTypes.object.isRequired,
    myTransaction: PropTypes.object.isRequired,
    changePrice: PropTypes.func,
    toastOpen: PropTypes.func.isRequired,
};

export default translate()(Order);
