import React from 'react';
import PropTypes from 'prop-types';
import update from 'immutability-helper';
import { translate } from 'react-i18next';
import equal from 'deep-equal';
import DeepChart from '../deepChart/deepChart';
import PriceChart from '../priceChart/priceChart';
import Prompt from '../prompt/prompt';
import './content.scss';

const pw = 480;
class Content extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            width: 600,
            height: 360,
            type: 2,
            margin: { left: 20, right: 60, top: 10, bottom: 20 },
            open: true,
            toggle: false,
            kType: 'hour'
        };

        this.option = 'hour';
        this.changeChart = this.changeChart.bind(this);
        this.changeKType = this.changeKType.bind(this);
        this.getKChart = this.getKChart.bind(this);
        this.showChart = this.showChart.bind(this);
        this.renderSelect = this.renderSelect.bind(this);
        this.toggleSelect = this.toggleSelect.bind(this);
        this.clickOutside = this.clickOutside.bind(this);
    }

    componentDidMount()
    {
        setTimeout(() =>
        {
            this.setField();
        }, 100);

        window.addEventListener('resize', this.updateDimensions.bind(this));
        window.addEventListener('click', this.clickOutside);
    }

    shouldComponentUpdate(nextProps, nextState)
    {
        if (equal(nextProps, this.props) && equal(nextState, this.state))
        {
            return false;
        }
        else
        {
            return true;
        }
    }

    componentWillUnmount()
    {
        window.removeEventListener('resize', this.updateDimensions.bind(this));
        window.removeEventListener('click', this.clickOutside);
    }

    setField()
    {
        if (!this.field) return;
        const width = this.field.offsetWidth - 30;
        const height = this.field.offsetHeight - 20;
        this.setState(update(this.state, {
            width: { $set: width },
            height: { $set: height }
        }));
        if (window.innerWidth < pw)
        {
            this.setState(update(this.state, {
                margin: {
                    $set: { left: 20, right: 55, top: 20, bottom: 30 }
                }
            }));
        }
    }

    getKChart(volume)
    {
        const { actions } = this.props;
        const { timeunitChange, kLineGet$ } = actions.bookActions;
        timeunitChange(volume);

        kLineGet$();
    }

    clickOutside(e)
    {
        const toggle = document.querySelector('.c_content .toggle');
        const select = document.querySelector('.c_content .select');
        if (toggle && !select.contains(e.target))
        {
            this.setState(update(this.state, {
                toggle: { $set: false }
            }));
        }
    }

    changeChart(type)
    {
        if (type === 1)
        {
            this.setState(update(this.state, {
                margin: {
                    left: { $set: 60 },
                    right: { $set: 90 },
                    bottom: { $set: 40 }
                },
                type: { $set: type }
            }));
        }
        else
        {
            this.setState(update(this.state, {
                margin: {
                    left: { $set: 20 },
                    right: { $set: 60 },
                    bottom: { $set: 20 }
                },
                type: { $set: type }
            }));
        }
    }

    changeKType(e)
    {
        e.stopPropagation();
        const type = e.currentTarget.dataset.type;
        switch (type)
        {
            case 'minute':
                this.getKChart(60 * 10 * 1000);
                break;
            case 'hour':
                this.getKChart(60 * 60 * 1000);
                break;
            case 'day':
                this.getKChart(60 * 60 * 24 * 1000);
                break;
            case 'month':
                this.getKChart(60 * 60 * 24 * 30 * 1000);
                break;
            default:
                this.getKChart(60 * 60 * 1000);
        }
        this.setState(update(this.state, {
            toggle: { $set: false },
            kType: { $set: type }
        }));
    }

    showChart()
    {
        this.setState(update(this.state, {
            open: { $set: !this.state.open }
        }));
    }

    toggleSelect()
    {
        this.setState(update(this.state, {
            toggle: { $set: !this.state.toggle },
        }));
    }

    updateDimensions()
    {
        if (this.field)
        {
            this.setState({
                width: this.field.offsetWidth - 30,
                height: this.field.offsetHeight - 20,
            });

            if (window.innerWidth < pw)
            {
                this.setState(update(this.state, {
                    margin: {
                        $set: { left: 20, right: 55, top: 20, bottom: 30 }
                    }
                }));
            }
            else if (window.innerWidth >= pw && this.state.type === 1)
            {
                this.setState(update(this.state, {
                    margin: {
                        left: { $set: 50 },
                        right: { $set: 80 },
                        bottom: { $set: 40 }
                    }
                }));
            }
            else
            {
                this.setState(update(this.state, {
                    margin: {
                        left: { $set: 20 },
                        right: { $set: 60 },
                        bottom: { $set: 20 }
                    }
                }));
            }
        }
    }

    renderChart()
    {
        const { width, height, type, margin } = this.state;
        const { sell, buy, data } = this.props;

        if (type === 1)
        {
            return (
                <DeepChart margin={margin} sell={sell} buy={buy} width={width} height={height} />
            );
        }
        else
        {
            return (
                <PriceChart margin={margin} data={data} height={height} width={width} />
            );
        }
    }

    renderSelect()
    {
        if (this.state.type === 2)
        {
            let type;
            const { t } = this.props;
            switch (this.state.kType)
            {
                case 'minute':
                    type = t('chart.10minute');
                    break;
                case 'hour':
                    type = t('chart.hour');
                    break;
                case 'day':
                    type = t('chart.day');
                    break;
                case 'month':
                    type = t('chart.month');
                    break;
                default:
                    type = t('chart.hour');
            }
            return (
                <div className="unit">
                    <div className="select" onClick={this.toggleSelect}>
                        <span>{type}</span>
                        <img src="/img/icon/ic-dropdown-p.svg" alt="dropdown" />
                        <div className={this.state.toggle ? 'toggle' : 'toggle close'}>
                            <div data-type="minute" onClick={this.changeKType}>{t('chart.10minute')}</div>
                            <div data-type="hour" onClick={this.changeKType}>{t('chart.hour')}</div>
                            <div data-type="day" onClick={this.changeKType}>{t('chart.day')}</div>
                            <div data-type="month" onClick={this.changeKType}>{t('chart.month')}</div>
                        </div>
                    </div>
                </div>
            );
        }
        else
        {
            return null;
        }
    }

    render()
    {
        const { t } = this.props;
        const { type } = this.state;
        return (
            <div className="c_content">
                <div className="title">
                    <div className="control">
                        <div>
                            <a className={type === 1 ? 'switch active' : 'switch'} onClick={() => { this.changeChart(1); }}>
                                {t('chart.depthChart')}
                                <Prompt message={t('prompt.depth')} />
                            </a>
                            <a className={type === 2 ? 'switch active' : 'switch'} onClick={() => { this.changeChart(2); }}>
                                {t('chart.priceChart')}
                                <Prompt message={t('prompt.candlestick')} />
                            </a>
                        </div>
                    </div>
                </div>
                <div className={this.state.open ? 'dashboard' : 'dashboard close'}>
                    {this.renderSelect()}
                    <div className="chart" ref={(el) => { this.field = el; }}>
                        {this.renderChart()}
                    </div>
                </div>
            </div>
        );
    }
}

Content.defaultProps = {
    t: () => {},
};

Content.propTypes = {
    sell: PropTypes.array.isRequired,
    buy: PropTypes.array.isRequired,
    data: PropTypes.array.isRequired,
    t: PropTypes.func,
    actions: PropTypes.object.isRequired
};

export default translate()(Content);
